<template>
  <li class="gift-cell" @click="gotoDetail">
    <p class="gift-img"><img :src="gift.imgUrl" v-if="gift.imgUrl"/><img :src="giftDefaultImg" v-else/></p>
    <p v-if="gift.title.length > 9" class="gift-name">{{gift.title.substring(0, 9)}}...</p>
    <p v-else class="gift-name">{{gift.title}}</p>
    <p class="gift-score">
      <i class="icon-coin"></i>
      <span>{{gift.point}}积分</span>
    </p>
  </li>
</template>
<script>
  import giftDefaultImg from './images/gift-default@2x.png'
  export default {
    name: 'gift-cell',
    props: {
      gift: {
        type: Object,
        required: true
      }
    },
    data() {
      return {
        giftDefaultImg
      }
    },
    computed: {},
    methods: {
      gotoDetail(e) {
        this.$emit('onDetail', e)
      }
    }
  }
</script>
<style lang="postcss" scoped>
  .gift-cell {
    float: left;
    margin: 0 6px 15px 0;
    width: 49%;
    height: 132px;
    &:nth-of-type(even) {
      margin-right: 0;
    }
    .gift-img {
      height: 90px;
      overflow: hidden;
      text-align: center;
      display: flex;
      border-radius: 5px;
      border: none;
      img {
        width: 100%;
        height: 100%;
        align-self: center;
      }
    }
    .gift-name {
      font-size: 13px;
      color: #333;
      padding: 8px 3px 2px 0;
    }
    .gift-score {
      font-size: 11px;
      color: #666;
      .icon-coin {
        display: inline-block;
        width: 10px;
        height: 9px;
        background-image: url('images/iconCoin@2x.png');
        background-size: cover;
      }
    }
  }
</style>



