<template>
  <div class="header-wrapper" :class="{'iphone-normal':iphoneNormal,'iphoneX':iphoneX}">
    <mt-header
      v-bind:class="[{header: true,
     'opposition': opposition,
     'animated': true,
     'fadeOutUp': fadeOutUp,
     'fadeInDown': fadeInDown,
     'transition': fadeOut || fadeIn,
     'state-bar': stateBar,
     'opacity':fadeInOpacity}, customClass]"
      :title="title"
      :fixed="fixed"
    >
      <mt-button slot="left" @click="leftHandle">
        <i :class="left.icon" v-show="left.show !== false">{{left.text}}</i>
      </mt-button>
      <div v-if='!title' slot="center">
        <slot></slot>
      </div>
      <mt-button slot="right" @click="rightHandle">
        <i :class="right.icon" v-show="right.show !== false">{{right.text}}</i>
      </mt-button>
    </mt-header>
    <div v-show="fixed && placeholder" :class="[{'state-bar': stateBar,'opacity':fadeInOpacity},'header-padding']"></div>
    <div v-show="(!fadeInSwitch && subHeader.show && sole) || (!fadeInSwitch && subHeader.show && fixed)" class="sole-padding"></div>
    <SubHeader
      ref="subHeader"
      :class="[{'opacitySub':fadeInOpacity},subHeader.className]"
      v-show="subHeader.show"
      :fixed="(fixed && subHeader.fixed && !sole) || fadeInSwitch"
      :fadeOut="fadeOut"
      :fadeInDown="fadeInDown"
      :fadeOutUp="fadeOutUp"
      :fullView="fullView"
      :sole="sole"
    >
      <div slot="sub">
        <slot name="subHeader"></slot>
      </div>
    </SubHeader>
  </div>
</template>
<script>
  import scrollMonitor from 'utils/scroll-monitor'
  import SubHeader from 'components/Header/SubHeader'
  import MtHeader from './Header.vue'
  import ua from 'utils/ua'
  export default {
    name: 'Header',
    components: {
      SubHeader: SubHeader,
      MtHeader
    },
    props: {
      title: {
        type: String,
      },
      fullView: {
        type: Boolean,
        'default': false
      },
      fixed: {
        type: Boolean,
        'default': false
      },
      stateBar: {
        type: Boolean,
        'default': false
      },
      noPlaceholder: {
        type: Boolean,
        'default': false
      },
      customClass: {
        type: String,
        'default': ''
      },
      left: {
        type: Object,
        'default': function () {
          return {
            show: true,
            text: '',
            icon: 'icon left'
          }
        }
      },
      right: {
        type: Object,
        'default': function () {
          return {
            show: true,
            text: '',
            icon: 'icon share-p',
          }
        }
      },
      subHeader: {
        type: Object,
        'default': function(){
          return {
            show: false,
            fixed: false,
            soleFixed: false,
            className: ''
          }
        }
      },
      fadeOut: {
        type: Boolean,
        'default': false
      },
      fadeIn: {
        type: Boolean,
        'default': false
      },
      fadeInOpt: {
        type: Object,
        'default': function(){
          return {
            height: 300
          }
        }
      },
      opposition: {
        type: Boolean,
        'default': false
      }
    },
    data() {
      return {
        fadeOutUp: false,
        fadeInDown: false,
        iphoneNormal: false,
        iphoneX: false,
        placeholder: true,
        fadeInSwitch: false,
        solePaddingSwitch: false,
        fadeInOpacity: false,
        sole: false,
      }
    },
    created(){
      // 适配ios
      this.setHeight();
      // 设置fade默认类型
      if (this.fadeIn && this.fixed) {
      // 取消头部fixed 占位
        this.placeholder = false
        this.fadeInSwitch = true
        this.fadeInOpacity = true
      }
      if (this.fadeOut) {
        this.fadeInSwitch = false
        this.fadeInOpacity = false
        this.placeholder = true
      }
      if (this.noPlaceholder) {
        this.placeholder = false
      }
    },
    computed: {},
    mounted() {
      //fadeIn 和 fadeOut 方式二选一
      window.onresize = () => {
        this.setHeight();
      }
      // 获取subheader offsetTop
      const subOffsetTop = this.$refs.subHeader.$el.offsetTop
      if (this.fadeOut || this.fadeInSwitch || this.subHeader.soleFixed) {
        scrollMonitor((dir, scrollTop) => {
          if (this.fadeInSwitch) {
            // 滑动透明 ----> 显示
            if (dir === 'up' && scrollTop >= (this.fadeInOpt.height * 2 / 3)) {
              this.fadeInOpacity = false
            } else if (dir === 'down' && scrollTop < (this.fadeInOpt.height * 2 / 3)) {
              this.fadeInOpacity = true
            }
          } else if (this.fadeOut) {
            // 滑动消失
            if (dir === 'up' && scrollTop >= 300) {
              if (this.fadeOutUp) {
                return
              }
              this.fadeInDown = false
              this.fadeOutUp = true
            } else if (dir === 'down') {
              if (this.fadeInDown) {
                return
              }
              this.fadeOutUp = false
              this.fadeInDown = true
            }
          } else if (this.subHeader.soleFixed && !this.fixed) {
            // subheader 滑动 单独固定在顶部
            if (scrollTop >= subOffsetTop) {
              this.sole = true;
            } else {
              this.sole = false;
            }
          }
        })
      }
    },
    methods: {
      leftHandle(e) {
        this.$emit('onLeft', e)
      },
      rightHandle(e) {
        this.$emit('onRight', e)
      },
      setHeight(){
        if (this.fullView) {
          if (ua.ios) {
            if (ua.isIphoneX) {
              this.iphoneX = true;
            } else {
              this.iphoneNormal = true;
            }
          }
        }
      }
    }
  }
</script>
<style lang="postcss" scoped>
  @keyframes dd {
    from {
      opacity: 1;
    }
    50% {
      opacity: .5;
    }
    to {
      opacity: 1;
    }
  }
  .header-wrapper {
    .header {
      background-color: #fff;
      color: #000;
      height: 58px;
      line-height:58px;
      font-size: 17px;
      transition: all .1s ease;
      border-bottom: 1px solid rgba(221,221,221,1);
      clear: both;
      &.opposition {
        background-color: #fd5729;
        color: #fff;
        animation: dd .1s linear;
      }
      &.transition{
        transition:all 1s ease;
        i {
          transition:color 1s ease;
        }
      }
      &.state-bar {
        height: 66px;
        padding-top: 20px;
      }
      i{
        font-size: 22px;
        color:#333;
      }
      &.opacity{
        background: none;
        border-bottom:1px solid rgba(221,221,221,0);
        h1.mint-header-titlee{
          opacity: 0;
        }
        i{
          color:#fff;
        }

      }

    }
    .sole-padding{
      height: 40px;
    }
    .header-padding {
      height: 58px;
      &.state-bar {
        height: 66px;
      }
    }
    &.iphone-normal{
      .header{
        height: 64px;
        padding-top:16px;
      }
      .header-padding {
        height: 64px;
        &.state-bar {
          height: 82px;
        }
      }
    }
    &.iphoneX{
      .header {
        height: 88px;
        padding-top:32px;
        &.state-bar {
          height: 76px;
          padding-top: 30px;
        }
      }
      .header-padding {
        height: 88px;
        &.state-bar {
          height: 104px;
        }
      }
    }
  }
</style>
<style lang="postcss">
  .mint-header-title{
    transition:opacity 1.5s ease;
    opacity: 1;
  }
  .opacity {
    .mint-header-title{
      opacity: 0;
    }
  }
</style>



