const ua = navigator.userAgent
const matchJcy = ua.match(/jcy\/(.*)/)
const strMatchJcy = !!matchJcy && matchJcy[1]
const matchDevice = ua.match(/deviceName\/(.*)/)
export const wx = ua.indexOf('MicroMessenger') !== -1
export const android = !!ua.match(/(Android);?[\s/]+([\d.]+)?/)
export const iPad = !!ua.match(/(iPad).*OS\s([\d_]+)/)
export const iphone = !iPad && !!ua.match(/(iPhone\sOS)\s([\d_]+)/)
export const jcy = !!matchJcy
export const jcyVersion = jcy && strMatchJcy.split(' deviceName')[0]
export const ios = iPad || iphone
export const isIphoneX = /iphone/gi.test(navigator.userAgent) && (screen.height === 812 && screen.width === 375)
export const deviceName = !!matchDevice && matchDevice[1]
export default {
  ios,
  iphone,
  iPad,
  android,
  wx,
  jcy,
  jcyVersion,
  isIphoneX,
  deviceName
}



