            </li>
            <!-- <li @click="routerLink({path: '/invite', newWebView: true, auth: true})">
              <span class="invite">西湖论金邀请函</span>
              <span class="arrow-right"></span>
            </li> -->
            <li @click="routerLink({path: '/invite', newWebView: true, auth: true, ylhAuth: true, buryId: 21, ylhInvitation: true, canLink: (brokerNo && shareCustNo)})">
