<style>
body {
  background: #f6f6f6 !important;
}
.user-account-main {
  .top-bg {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 999;
    background-image: linear-gradient(158deg, #FF9053 0%, #FD5729 97%);
    .header-wrapper .header {
      background: transparent;
      &.self-header {
        border: none;
        color: #fff;
        font-size: 17px;
        i.left{
          line-height: 45px;
          color: #fff;
          font-size: 28px;
        }
      }
    }
  }
  .top-slot {
    height: 145px;
  }
}
</style>

<style scoped>
    .user-account-main {
      .basicInfo {
        color: #fff;
        height: 56px;
        padding: 15px 20px;
        position: relative;
        .avatar {
          float: left;
          margin-right: 8px;
          width: 56px;
          height: 56px;
          border-radius: 50%;
        }
        .name {
          font-size: 18px;
          font-weight: 500;
          margin: 2px 0;
          height: 25px;
        }
        .myscore {
          span {
            display: inline-block;
            width: 72px;
            height: 18px;
            background: #FFBE00;
            font-size: 12px;
            border-radius: 10px 0 10px 10px;
            text-align: center;
          }
        }
        .newnotice-box {
          position: absolute;
          top: 20px;
          right: 23px;
          .icon-newnotice {
            position: relative;
            display: inline-block;
            width: 24px;
            height: 20px;
            background: url('./images/icon-notice@2x.png') no-repeat;
            background-size: 100% 100%;
          }
          .icon-circle {
            position: absolute;
            top: -4px;
            right: -5px;
            display: inline-block;
            width: 6px;
            height: 6px;
            background: #FF3D33;
            border-radius: 50%;
            border: 1px solid #FFF;
          }
        }
      }
      .asset-overview {
        position: relative;
        background: #fff;
        padding: 18px 20px;
        height: 54px;
        margin-bottom: 10px;
        .total {
          .item-key {
            display: block;
            font-size: 13px;
            color: #aaa;
          }
          .item-value {
            font-size: 30px;
            font-weight: 500;
            color: #FD5729;
          }
        }
        .trade {
          position: absolute;
          top: 35px;
          right: 19px;
          font-size: 13px;
          color: #aaa;
          .icon-next {
            display: inline-block;
            width: 7px;
            height: 12px;
            margin: 0 0 -2px 0;
            background: url('./images/icon-next.png') no-repeat;
            background-size: 100% 100%;
          }
        }
      }
      .asset-detail {
        background: #fff;
        margin-bottom: 10px;
        ul {
          overflow: hidden;
          clear: both;
          li {
            width: calc((100% - 2px) / 2);
            float: left;
            font-size: 16px;
            color: #333;
            position: relative;
            height: 46px;
            padding: 16px 0 14px;
            &:nth-child(1),
            &:nth-child(2) {
              border-bottom: 1Px solid #F2F2F2;
            }
            &:nth-child(1),
            &:nth-child(3) {
              border-right: 1Px solid #F2F2F2;
            }
            p {
              padding-left: 18px;
            }
            .num {
              font-size: 15px;
              color: #666;
              margin: 5px 0 0 28px;
            }
            .login-tip {
              font-size: 13px;
              color: #FFA32A;
              margin: 5px 0 0 28px;
            }
            .easybao::before {
              content: '';
              display: inline-block;
              margin: 0 5px -5px 0;
              width: 24px;
              height: 24px;
              background: url('images/tag-easybao@2x.png') no-repeat;
              background-size: 100% 100%;
            }
            .private::before {
              content: '';
              display: inline-block;
              margin: 0 5px -6px 0;
              width: 24px;
              height: 24px;
              background: url('images/tag-private@2x.png') no-repeat;
              background-size: 100% 100%;
            }
            .public::before {
              content: '';
              display: inline-block;
              margin: 0 5px -5px 0;
              width: 24px;
              height: 24px;
              background: url('images/tag-public@2x.png') no-repeat;
              background-size: 100% 100%;
            }
          }
        }
      }
      .item-list {
        ul {
          li {
            height: 50px;
            line-height: 50px;
            padding: 0 13px;
            background: #fff;
            border-bottom: 1Px solid #F2F2F2;
            font-size: 15px;
            color: #666;
            position: relative;
            &.mb10 {
              margin-bottom: 10px;
            }
            .arrow-right {
              position: absolute;
              top: 14px;
              right: 13px;
              display: inline-block;
              width: 6px;
              height: 12px;
              margin: 5px 0 0 0;
              background: url('../../images/icon/arrow-right@2x.png') no-repeat;
              background-size: 100% 100%;
            }
            .identity-info {
              position: absolute;
              top: 0;
              right: 30px;
              text-align: right;
              color: #aaa;
              font-size: 14px;
            }
            .identity::before {
              content: '';
              display: inline-block;
              margin: 0 5px -6px 0;
              width: 24px;
              height: 24px;
              background: url('images/tag-identity@2x.png') no-repeat;
              background-size: 100% 100%;
            }
            .faq::before {
              content: '';
              display: inline-block;
              margin: 0 5px -7px 0;
              width: 24px;
              height: 24px;
              background: url('images/tag-faq@2x.png') no-repeat;
              background-size: 100% 100%;
            }
            .invite::before {
              content: '';
              display: inline-block;
              margin: 0 5px -5px 0;
              width: 24px;
              height: 24px;
              background: url('images/tag-invite@2x.png') no-repeat;
              background-size: 100% 100%;
            }
            .friend::before {
              content: '';
              display: inline-block;
              margin: 0 5px -5px 0;
              width: 24px;
              height: 24px;
              background: url('images/tag-friend@2x.png') no-repeat;
              background-size: 100% 100%;
            }
            .risktest::before {
              content: '';
              display: inline-block;
              margin: 0 5px -7px 0;
              width: 24px;
              height: 24px;
              background: url('images/tag-risktest@2x.png') no-repeat;
              background-size: 100% 100%;
            }
            .feedback::before {
              content: '';
              display: inline-block;
              margin: 0 5px -7px 0;
              width: 24px;
              height: 24px;
              background: url('images/tag-feedback@2x.png') no-repeat;
              background-size: 100% 100%;
            }
          }
        }
      }
    }
    @media only screen and (device-width: 375px) and (device-height: 812px) {
      .user-account-main {
        .top-slot {
          padding-top: 30px;
        }
      }
    }
</style>



