<template>
  <div class="activity-activitylist-main">
    <div>
      <Header
        :fixed="fixed" 
        :fullView="fullView" 
        :right="right"
        @onLeft="goBack"
        :customClass="customClass"
      >
      <div class="title-tab">
        <mt-navbar v-model="selected">
          <mt-tab-item id="topic">专题</mt-tab-item>
          <mt-tab-item id="activity">活动</mt-tab-item>
        </mt-navbar>
      </div>
      </Header>
      <mt-loadmore :top-method="loadTop"  :bottom-all-loaded="allLoaded" @top-status-change="handleTopChange" ref="loadmore">
        <div :style="windowH">
          <mt-tab-container v-model="selected" :swipeable="true">
              <mt-tab-container-item id="activity">
                <div class="activity topic">
                  <div class="list" v-if="operateBanners.length">
                    <div class="card-item" v-for="(item, index) in operateBanners" :key="index"  >
                      <a href="javascript:;" @click="toOutLink(item.linkUrl)">
                        <div class="radius-img"><img :src="item.imageUrl" alt=""></div>
                        <h2>{{item.adName}}</h2>
                      </a>
                      <!-- <span class="types" :class="{'types-start':status == 0 || status == 1 || status == 3, 'types-end':status == 2}" >{{item.statusName}}</span> -->
                    </div>
                  </div>
                  <div class="no-data" v-if="!operateBanners.length && isLoaded">更多精彩活动，敬请期待</div>
                </div>
              </mt-tab-container-item>
            <!-- 专题列表 -->
            <mt-tab-container-item id="topic">
              <div class="activity topic">
                <div class="list" v-if="operateBanners.length">
                  <div class="card-item" v-for="(item, index) in operateBanners" :key="index"  >
                    <a href="javascript:;" @click="toOutLink(item.linkUrl)">
                      <div class="radius-img"><img :src="item.imageUrl" alt=""></div>
                      <h2>{{item.adName}}</h2>
                    </a>
                    <!-- <span class="types" :class="{'types-start':status == 0 || status == 1 || status == 3, 'types-end':status == 2}" >{{item.statusName}}</span> -->
                  </div>
                </div>
                <div class="no-data" v-if="!operateBanners.length && isLoaded">更多精彩活动，敬请期待</div>
              </div>
            </mt-tab-container-item>
          </mt-tab-container>
        </div>
        <div slot="top">
          <TopStatus :status="topStatus"></TopStatus>
        </div>
      </mt-loadmore>
    </div>
  </div>
</template>
<script>
  import Header from 'components/Header'
  import ua from 'utils/ua'
  import TopStatus from 'components/TopStatus'
  export default {
    name: 'Activity',
    data() {
      return {
        fixed: true,
        fullView: true,
        right: {
          show: false
        },
        back: {
          show: true,
          text: '',
          icon: 'icon-back',
        },
        customClass: 'self-header',
        topStatus: '',
        selected: "topic",
        status: 3,
        activeList: [],
        operateBanners: [],
        tags: '1',
        currentTypeId: 0,
        endDay: '',
        allLoaded: false,
        bottomAllLoaded: false,
        pageNo: 1,
        windowH: {},
        isLoaded: false,
      }
    },
    components: {
      Header,
      TopStatus
    },
    mounted(){
      let h = (window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight)
      this.windowH = { 'min-height': h + 'px'}
      this.fetchTopicList()
    },
    watch: {
      selected(val) {
        if (val === "topic") {
          this.tags = '2'
          console.log('切换到专题');
        } else {
          this.tags = '1'
          console.log('切换到活动');
        }
        this.fetchTopicList()
      }
    },
    computed: {
    },
    methods: {
      handleTopChange(status) {
        this.topStatus = status
      },
      routerLink(opt){
        this.$router.push(opt.path)
      },
      toOutLink(path){
        location.href = path
      },
      fetchTopicList(callback){
        this.operateBanners = [{
          adName: '金融高峰论坛邀请函',
          linkUrl: '',
          imageUrl: 'https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=4093127378,1099634536&fm=26&gp=0.jpg'
        }, {
          adName: '邀请好友送豪礼',
          linkUrl: '',
          imageUrl: 'https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=59965771,3100329105&fm=26&gp=0.jpg'
        }, {
          adName: '国庆带你装逼带你飞',
          linkUrl: '',
          imageUrl: 'https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=2971000125,1507207318&fm=15&gp=0.jpg'
        }]
        callback && callback()
        // 获取专题列表
        // this.isLoaded = false
        // api.getOperateBanners({
        //   tags: this.tags || '2'
        // }).then(res => {
        //   if (res.code === 0 && res.data){
        //     this.operateBanners = res.data.operateBanners || []
        //   }
        //   this.isLoaded = true
        //   callback && callback()
        // }).catch(() => {
        //   this.isLoaded = true
        //   callback && callback()
        // })
      },
      loadTop() {
         // 下拉更新活动
        this.fetchTopicList(() => {
          this.$refs.loadmore && this.$refs.loadmore.onTopLoaded && this.$refs.loadmore.onTopLoaded()
        })
        // this.allLoaded = false
      },
      // loadBottom () {
      //   if (this.selected === 'activity') {
      //     this.pageNo++
      //     this.fetchList({
      //       pageNo: this.pageNo,
      //       oldData: this.activityList,
      //       callback: () => {
      //         this.$refs.loadmore.onBottomLoaded()
      //       }
      //     })
      //     this.allLoaded = true
      //   }
      // },
      goBack(){
        this.$router.back()
      }
    }
  }
</script>
<style>
.activity-activitylist-main {
  .header-wrapper .header {
    &.self-header {
      font-size: 17px;
      i.left{
        line-height: 45px;
        font-size: 28px;
      }
    }
  }
  .mint-navbar .mint-tab-item{
      margin: 0 50px;
      padding: 15px 10px 12px;
      &:first-child{
        margin-right:20px;
      }
      .mint-tab-item-label{
        font-size:16px !important;
        font-weight: 500;
        color:#666;
      }
    }
    .mint-navbar .mint-tab-item.is-selected{
      border-bottom: 3px solid #fd5729;
      margin-bottom: 0px;
      .mint-tab-item-label{
        color: #333;
        font-weight: 500;
      }
    }
}
</style>
<style scoped>
  .activity-activitylist-main {
    /* 此处定义当前页面样式 */
    .header i.left{
      font-size:28px;
      margin-top:10px;
      line-height: .45rem;
    }
    .title-tab{
      .mint-navbar{
        height:45px;
        .mint-tab-item{
          .mint-tab-item-label{
            font-size:15px;
            font-weight: 400;
          }
        }
      }
      
    }
    .no-data{
      color:#999;
      height:128px;
      font-size:14px;
      padding-top:106px;
      text-align:center;
      background:url("./images/no-data.png") center 0 no-repeat;
      background-size:86px;
      margin-top:120px;
    }
    .activity-tab-btn{
      background:#fff;
    }
    .activity{
      padding-bottom:100px;
      .tag{
        background:#fff;
        min-height: 57px;
        box-sizing:border-box;
        padding: 15px 0 5px 20px;
        span.tag-item{
          width: calc((85.8%-20px)/4);
          text-align:center;
          height: 30px;
          line-height: 30px;
          display: inline-block;
          background:#f6f6f6;
          border-radius:5px;
          margin-right:10px;
          margin-bottom:10px;
          font-size: 12px;
          box-sizing: border-box;
          &.all-item{
            margin-right:5px;
          }
          &.last-line{
            margin-right:0px;
          }
          &.tag-active{
            background:#ffede8;
            color:#fd5729;
          }
          &:nth-of-type(4n) {
            margin-right:0px;
          }
        }
      }
      .list{
        padding:0 20px;
        .card-item{
          position: relative;
          margin-top:15px;
          border-radius:5px;
          background:#fff;
          h2{
            font-size: 16px;
            color: #333;
            height: 22px;
            line-height: 22px;
            padding: 14px 15px 14px 15px;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
          }
          p{
            font-size:12px;
            padding:0 12px;
            color:#AAA;
            .signNum{
              color:red;
            }
            .endTime{
              float: right;
            }
          }
          a{
            color:#333;
          }
          .radius-img{
            border-radius:5px 5px 0 0;
            overflow: hidden;
            height: 135px;
          }
          img{
            width:100%;
            height: 135px;
          }
          .types{
            width: 50px;
            height: 23px;
            position:absolute;
            right:-4px;
            top:8px;
            font-size: 10px;
            text-align: center;
            line-height:28px;
            color:#fff;
            &.types-start{
              background: url("./images/start.png") 0 0 no-repeat;
              background-size:50px 23px;
            }
            &.types-end{
              background: url("./images/end.png") 0 0 no-repeat;
              background-size:50px 23px;
            }
          }
        }
      }
    }
  }
</style>



