<template>
  <div class="mint-loadmore-top" style="color: #aaa">
    <p v-show="status !== 'loading'">
      <img src="./load.png" width="22" alt="" style="vertical-align: middle">
      <span :class="{ 'rotate': status === 'drop' }" style="vertical-align: middle; font-size: 14px;margin-left: 5px">下拉刷新</span>
    </p>
    <p v-show="status === 'loading'">
      <!--<i class="spinner"></i>-->
      <img src="./load1.png" width="22" class="loading" alt="" style="vertical-align: middle">
      <span style="font-size: 14px;vertical-align: middle; margin-left: 5px">松开刷新</span>
    </p>
  </div>
</template>
<script>
  export default {
    name: 'topstatus',
    props: {
      status: {
        type: String
      }
    },
    data() {
      return {
      }
    },
    computed: {},
    methods: {}
  }
</script>
<style lang="postcss" scoped>
  @keyframes spinner {
    to {transform: rotate(360deg);}
  }
  @-webkit-keyframes rotation{
    from {-webkit-transform: rotate(0deg);}
    to {-webkit-transform: rotate(360deg);}
  }
  .loading {
    transform: rotate(17deg);
    animation: rotation 1s linear infinite;
  }
  .spinner {
    position: relative;
    &::before {
      content: '';
      box-sizing: border-box;
      position: absolute;
      top: 50%;
      left: 50%;
      width: 20px;
      height: 20px;
      margin-top: -10px;
      margin-left: -30px;
      border-radius: 50%;
      border-top: 2px solid #fd5729;
      border-right: 2px solid transparent;
      animation: spinner 1.3s linear infinite;
    }
  }
</style>



