<template>
  <div class="about-suggest-main">
    <Header
      :fixed="fixed" 
      :fullView="fullView" 
      :right="right"
      @onLeft="goBack"
      :customClass="customClass"
    >
    <span class="bold">意见反馈</span>
    </Header>
    <div class="group not-require">
      <mt-field label="联系人姓名" :attr="{ maxlength: 10 }" placeholder="请输入姓名" v-model="username" ></mt-field>
      <mt-field label="联系电话" :attr="{ maxlength: 20 }" placeholder="请输入手机号" type="number" v-model="contactNumber"></mt-field>
    </div>
    <div class="group require description">
      <mt-field placeholder="请输入问题描述（必填）" type="textarea" rows="8" :attr="{ maxlength: 200 }" v-model="problemDescription" @keyup.native.capture="checkInput"></mt-field>
      <span class="num-word">{{problemDescription.length > 200 ? 200 : problemDescription.length}}/200</span>
    </div>
    <div>
      <p class="group upload-label">问题上传截图</p>
      <div class="upload-group ">
        <Upload 
        :onGetToken="getQiniuToken"
        :onSuccess="onSuccess"
        :onRemove="onRemove"
        :size="5"
        :length="3"
        ></Upload>
        <p class="remark">支持最多上传3张图片，单张图片不大于5M</p>
      </div>
      
    </div>
    <Footer :fixed="fixed" footerType ="" class="ff">
      <div slot="otherType"  class="" ref="footerDelete">
        <!-- <div v-if="isDelete" class="delete-box"  :class="{'active':active}">图片拖至此处删除</div> -->
        <mt-button v-if="isWrited" type="primary" size="large" @click="onSubmit">提交反馈</mt-button>
        <mt-button v-else type="default" disabled size="large">提交反馈</mt-button>
      </div>
    </Footer>
    <div class="pop-mask" v-show="showPopDelete">
      <div class="pop-layer pop-delete">
        <div class="pop-content">要删除这张照片吗？</div>
        <div class="pop-bottom">
          <a href="javascript:;" class="submit" @click="delConfirm">确定</a>
          <a href="javascript:;" class="cancel" @click="showPopDelete=false">取消</a>
        </div>
      </div>
    </div>
    <div class="pop-mask" v-show="showPopSuccess">
      <div class="pop-layer pop-success">
        <div class="pop-top">提交成功</div>
        <div class="pop-content">已收到您的反馈</div>
        <div class="pop-bottom">
          <a href="javascript:;" class="submit" @click="successConfirm">确定</a>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import Header from 'components/Header'
  import ua from 'utils/ua'
  import toast from 'utils/toast'
  import Footer from 'components/Footer'
  import Upload from 'components/Upload'
  import emoji from 'constants/emoji'
  export default {
    name: 'Feedback',
    data() {
      return {
        fixed: true,
        fullView: true,
        right: {
          show: false
        },
        back: {
          show: true,
          text: '',
          icon: 'icon-back',
        },
        customClass: 'self-header',
        username: '',
        contactNumber: '',
        domain: '',
        problemDescription: '',
        imagesList: [],
        qiniuUploadUrl: 'http://up.qiniu.com',
        qiniuToken: '',
        footerType: '',
        fixed: true,
        id: 0,
        needId: null,
        isWrited: false,
        showPopDelete: false,
        showPopSuccess: false,
      }
    },
    components: {
      Footer,
      Upload,
      Header
    },
    mounted(){
    },
    computed: {
    },
    watch: {
      problemDescription(v){
        if (v.length > 0) {
          this.isWrited = true
        } else {
          this.isWrited = false
        }
      }
    },
    methods: {
      checkInput() {
        const regRule = emoji()
        if (this.problemDescription.match(regRule)) {
          this.problemDescription = this.problemDescription.replace(regRule, '')
          toast('不支持表情！')
        }
      },
      getQiniuToken(resolve, reject){
        // if (this.qiniuToken) {
        //   resolve({token: this.qiniuToken, domain: this.domain})
        // } else {
        //   api.getQiniuToken().then((res) => {
        //     if (res.code === 0 && res.data) {
        //       // 设置七牛token domain
        //       this.qiniuToken = res.data.token
        //       this.domain = res.data.domain
        //       resolve({token: this.qiniuToken, domain: this.domain})
        //     }
        //   }).catch((err) => {
        //     reject(err)
        //   })
        // }
      },
      onSuccess(url){
        this.imagesList.push(url)
        // 开放提交按钮
      },
      onRemove(opt){
        this.showPopDelete = true
        this.opt = opt
      },
      delConfirm() {
        this.imagesList.splice(this.opt.index, 1)
        this.opt.resolve()
        this.showPopDelete = false
      },
      onSubmit(){
        // if (this.isUploading) {
        //   toast('图片上传中，请稍后提交')
        //   return
        // }
        // if (this.submit === true || !this.problemDescription) {
        //   // 必填项
        //   return
        // }
        // this.submit = true
        // api.postProblemFeedback({
        //   username: this.username,
        //   contactNumber: this.contactNumber,
        //   problemDescription: this.problemDescription = this.problemDescription.replace(emoji(), ''),
        //   imageUrls: this.imagesList
        // }).then((res) => {
        //   if (res.code === 0) {
        //     this.showPopSuccess = true
        //   }
        //   this.submit = false
        // })
      },
      successConfirm() {
        this.toBack()
        this.showPopSuccess = false
      },
      toBack(){
        this.$router.back()
      },
      goBack() {
        this.$router.back()
      },
    }
  }
</script>
<style>
  .header-wrapper .header {
    &.self-header {
      font-size: 17px;
      i.left{
        line-height: 45px;
        font-size: 28px;
      }
    }
  }
</style>

<style lang="postcss">
    .about-suggest-main {
      /* 此处定义当前页面样式 */
      /*mint 组件样式*/
      font-size: 14px;
      .mint-cell-wrapper{
        background:none;
        padding:0 20px;
        border-bottom:1Px solid #F2F2F2;
        &:last-child{
          border:none;
        }
      }
      .mint-cell-text{
        color:#666;
        font-size: 14px;
      }
      .mint-button{
        height: 50px;
        border-radius: 0;
      }
      .mint-button--default{
        box-shadow: none;
        background: #D9D9D9;
        color:#fff;
      }
      input::-webkit-input-placeholder,textarea::-webkit-input-placeholder{
        color:#D9D9D9 ;
        font-size: 14px;
      }
      .mint-field-core{
        font-size:14px;
        color:#333;
      }
      .footer-wrapper{
        height: 50px;
        .footer{
          height: 50px;
        }
      }
      .group{
        margin-top:10px;
        position: relative;
        .num-word {
          position: absolute;
          right: 20px;
          bottom: 12px;
          font-size: 12px;
          color: #AAA;
        }
        .mint-field {
          height: 50px;
          .mint-cell-title {
            width: 85px;
          }
          .mint-cell-value {
            input {
              display: inline-block;
              padding-top: 8px;
            }
            input::-webkit-input-placeholder {
              color: #AAA;
              
            }
          }
        }
        &.description {
          .mint-field {
            height: 200px;
          }
          .mint-cell-value {
            textarea {
              display: inline-block;
              padding-left: 0;
            }
            textarea::-webkit-input-placeholder {
              color: #AAA;
              
            }
          }
        }
      }
      .upload-label{
        margin-bottom:8px;
        color:#aaa;
        font-size:13px;
        padding-left:20px;
      }
      .upload-group{
        background: #fff;
        height:151px;
        padding:20px 20px 10px 20px;
        box-sizing: border-box;
        .remark{
          font-size:12px;
          margin-top:18px;
          color:#AAA;
        }
      }
      .mint-button.is-disabled {
        background: #bbb;
        font-weight: bold;
      }
      .mint-button--primary{
        background: #FD5729;
        height:50px;
        line-height:50px;
        border-radius: 0;
        font-weight: bold;
      }
      .delete-box{
        background: red;
        color:#fff;
        text-align: center;
        &.active{
          background:#ddd ;
        }
      }
      .pop-mask {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(55,55,55,.5);
        z-index: 2002;
        display: flex;
        align-items: center;
        justify-content: center;
        .pop-layer {
          width: 260px;
          background-color: #fff;
          border-radius: 5px;
        }
      }
      .pop-delete {
        .pop-content {
          font-size: 16px;
          color: #222;
          text-align: center;
          margin: 27px 0 17px;
        }
        .pop-bottom {
          border-top: 1Px solid #F2F2F2;
          font-size: 16px;
          text-align: center;
          display: flex;
          a {
            flex: 1;
            display: block;
            padding: 10px 0;
            margin: 2px 0;
          }
          a.submit {
            color: #aaa;
            border-right: 1Px solid #F2F2F2;
          }
          a.cancel {
            color: #FD5729;
            font-weight: 500;
          }
        }
      }
      .pop-success {
        .pop-top {
          font-size: 16px;
          font-weight: 500;
          color: #222;
          text-align: center;
          margin: 18px 0 11px;
        }
        .pop-content {
          font-size: 15px;
          color: #666;
          text-align: center;
          margin: 0 0 17px;
        }
        .pop-bottom {
          border-top: 1Px solid #F2F2F2;
          font-size: 16px;
          text-align: center;
          display: flex;
          a {
            flex: 1;
            display: block;
            padding: 10px 0;
            margin: 2px 0;
          }
          a.submit {
            color: #FD5729;
            font-weight: 500;
            border-right: 1Px solid #F2F2F2;
          }
        }
      }
    }
</style>


