<template>
  <div class="user-account-trade-main">
    <self-header
      :fixed="fixed" 
      :fullView="fullView" 
      :right="right"
      @onLeft="goBack"
      :customClass="customClass"
    >
    <span class="bold">交易记录</span>
    </self-header>
    <mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" @top-status-change="handleTopChange" ref="loadmore">
      <div :style="windowH">
        <mt-navbar v-model="selected">
          <mt-tab-item id="3">分红</mt-tab-item>
          <mt-tab-item id="4">赎回</mt-tab-item>
          <mt-tab-item id="5">转让</mt-tab-item>
          <mt-tab-item id="6">其他</mt-tab-item>
        </mt-navbar>
        <mt-tab-container v-model="selected" class="trade-box">
          <mt-tab-container-item id="3">
            <ul v-if="tradeList.length">
              <li is="trade-cell" v-for="(item,index) in tradeList" :key="index" :trade="item"></li>
            </ul>
            <p class="noTrade" v-if="!tradeList.length && isLoaded">暂无数据</p>
          </mt-tab-container-item>
          <mt-tab-container-item id="4">
            <ul v-if="tradeList.length">
              <li is="trade-cell" v-for="(item,index) in tradeList" :key="index" :trade="item"></li>
            </ul>
            <p class="noTrade" v-if="!tradeList.length && isLoaded">暂无数据</p>
          </mt-tab-container-item>
          <mt-tab-container-item id="5">
            <ul v-if="tradeList.length">
              <li is="trade-cell" v-for="(item,index) in tradeList" :key="index" :trade="item"></li>
            </ul>
            <p class="noTrade" v-if="!tradeList.length && isLoaded">暂无数据</p>
          </mt-tab-container-item>
          <mt-tab-container-item id="6">
            <ul v-if="tradeList.length">
              <li is="trade-cell" v-for="(item,index) in tradeList" :key="index" :trade="item"></li>
            </ul>
            <p class="noTrade" v-if="!tradeList.length && isLoaded">暂无数据</p>
          </mt-tab-container-item>
        </mt-tab-container>
      </div>
      <div slot="top">
        <TopStatus :status="topStatus"></TopStatus>
      </div>
    </mt-loadmore>
    <!-- <p class="login-tip" v-else><a href="javascript:;" @click="goLogin">点击登录</a>，查看交易记录</p> -->
    <!-- <user-login :loginVisible="loginVisible" @closeLogin="closeL ogin"></user-login> -->
  </div>
</template>
<script>
  import selfHeader from 'components/Header'
  import TradeCell from './TradeCell'
  import TopStatus from 'components/TopStatus'

  export default {
    name: 'Trade',
    components: {
      selfHeader,
      TradeCell,
      TopStatus
    },
    data() {
      return {
        fixed: true,
        fullView: true,
        right: {
          show: false
        },
        back: {
          show: true,
          text: '',
          icon: 'icon-back',
        },
        customClass: 'self-header',
        selected: '3',   // 交易类型[0-所有类型,1-认购,2-申购,3-分红,4-赎回,5-转让,6-其它]
        accessToken: '',
        // loginVisible: false,
        tradeList: [],
        pageNo: 1,
        allLoaded: false,
        bottomAllLoaded: false,
        tradeType: 3,
        isLogin: null,
        topStatus: '',
        windowH: {},
        isLoaded: false,
      }
    },
    mounted() {
      this.fetchTradeList({
        pageNo: 1,
        tradeType: this.tradeType,
        oldList: []
      })
      let h = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight
      this.windowH = { 'min-height': h + 'px'}
    },
    watch: {
      selected(val) {
        this.tradeList = []
        this.isLoaded = false
        this.tradeType = +val
        this.fetchTradeList({
          pageNo: 1,
          tradeType: +val,
          oldList: []
        })
      }
    },
    computed: {
    },
    methods: {
      fetchTradeList(opt){
        this.tradeList = []
        opt.callback && opt.callback()
        // this.isLoaded = false
        // api.getPrivateTrade({
        //   pageNo: opt.pageNo,
        //   pageSize: 10,
        //   tradeType: opt.tradeType,
        // }).then(res => {
        //   if (res.code === 0 && res.data) {
        //     this.tradeList = opt.oldList.concat(res.data.data || [])
        //     opt.callback && opt.callback()
        //     if (res.data.totalPage <= res.data.pageNo) {
        //       // 加载完毕
        //       this.allLoaded = true
        //     }
        //   }
        //   this.isLoaded = true
        // }).catch(() => {
        //   opt.callback && opt.callback()
        //   this.isLoaded = true
        // })
      },
      loadTop() {
        this.pageNo = 1
        this.fetchTradeList({
          pageNo: this.pageNo,
          tradeType: this.tradeType,
          oldList: [],
          callback: () => {
            this.$refs.loadmore.onTopLoaded()
          }
        })
        this.allLoaded = false
      },
      loadBottom () {
        this.pageNo++
        this.fetchTradeList({
          pageNo: this.pageNo,
          tradeType: this.tradeType,
          oldList: this.tradeList,
          callback: () => {
            this.allLoaded = false
            this.$refs.loadmore.onBottomLoaded()
          }
        })
        this.allLoaded = true
      },
      handleTopChange(status) {
        // this.moveTranslate = 1
        this.topStatus = status
      },
      goBack() {
        console.log('goback')
        this.$router.back()
      },
      goLogin() {
        // this.loginVisible = true
        // this.$router.push({path: '/user/login'})
      },
      closeLogin() {
        // console.log('parent');
        // this.loginVisible = false
      }
    }
  }
</script>
<style>
  body {
    background: #f6f6f6;
  }
  .user-account-trade-main {
    .header-wrapper .header {
      &.self-header {
        font-size: 17px;
        i.left{
          line-height: 45px;
          font-size: 28px;
        }
      }
    }
  }
</style>

<style lang="postcss">
    .user-account-trade-main {
      .icon-back {
        display: inline-block;
        width: 25px;
        height: 25px;
        background-image: url('../../../images/icon/backWhite@2x.png');
        background-size: contain;
        background-repeat: no-repeat;
        margin: 5px 0 0 -6px;
      }
      .mint-navbar {
        display: block;
        text-align: left;
        padding-left: 13px;
        .mint-tab-item {
          display: inline-block;
          padding: 0;
          width: 64px;
          height: 24px;
          text-align: center;
          margin: 10px 0;
          .mint-tab-item-label {
            color: #666;
            font-size: 14px;
            line-height: 24px;
          }
          &.is-selected {
            border: 0;
            background: #FFEDE8;
            border-radius: 100px;
            .mint-tab-item-label {
              color: #FF5A23;
            }
          }
        }
      }
      .trade-box {
        margin-top: 10px;
        .noTrade {
          margin-top: 119px;
          text-align: center;
          font-size: 15px;
          color: #AAA;
          &::before {
            content: '';
            display: block;
            width: 86px;
            height: 86px;
            margin: 0 auto;
            margin-bottom: 22px;
            background: url('../../../images/noTrade@2x.png') no-repeat;
            background-size: 100% 100%;
          }
        }
      }
      .login-tip {
        margin-top: 165px;
        text-align: center;
        font-size: 14px;
        color: #000;
        a {
          color: #2C8BE6;
        }
      }
    }
</style>



