<template>
  <div class="loading" :style="cssText" v-show="show">
    <img class="rotate" :src="icon" alt="">
  </div>
</template>
<script>
  import icon from './loading@2x.png'
  export default {
    name: 'loading',
    props: {
      show: {
        type: Boolean,
        default: false
      },
      icon: {
        type: String,
        default: icon
      },
      cssText: {
        type: String,
        default: ''
      }
    },
    computed: {},
    methods: {}
  }
</script>
<style lang="postcss" scoped>
  .rotate {
    animation: rotate 2s linear infinite;
  }
  @-webkit-keyframes rotate {
    from {
      transform: rotate(360deg);
    }
    to {
      transform: rotate(0deg);
    }
  }
  .loading {
    text-align: center;
    width: 60px;
    height: 60px;
    line-height: 58px;
    background: #fff;
    border-radius: 5px;
    position: fixed;
    left: calc(50% - 40px);
    top: calc(50% - 40px);
    img {
      width: 32px;
      height: 32px;
      vertical-align: middle;
    }
  }
</style>



