<template>
  <div>
    <p class="company" v-show="fund.showAdministrator">{{fund.administratorname}}</p>
    <div class="fund-cell">
      <h3 @click="gotoDetail(fund.id, fund.productName, fund.fundCode)">
        <span class="risk-level" v-if="fund.riskInfo">{{fund.riskInfo}}</span>
        <span v-if="fund.productName.length>20">{{fund.productName.substring(0, 20)}}...</span>
        <span v-else>{{fund.productName}}</span>
      </h3>
      <div class="fund-intro" @click="gotoDetail(fund.id, fund.productName, fund.fundCode)">
        <p class="rate">
          <span class="item-value" v-if="fund.incomeRate==='--' || fund.incomeRate==='浮动'">{{fund.incomeRate}}</span>
          <span class="item-value" v-else>{{fund.incomeRate}}%</span>
          <span class="item-key">投资目标业绩（年化）</span>
        </p>
        <p class="invest">
          <span class="item-value">{{investAmount}}</span>
          <span class="item-key">投资金额（元）</span>
        </p>
        <p class="income">
          <span class="item-value">{{totalIncome}}</span>
          <span class="item-key">累计收益（元）</span>
        </p>
      </div>
      <div class="fund-town" v-if="showTown" @click="gotoTownIntro(fund.jcTenTown, fund.townInfo)">
        <span class="townname" v-if="fund.jcTenTown">{{fund.jcTenTown.ecmName}}</span>
        <span class="townname" v-else>{{fund.townInfo.shortTownName || '特色小镇项目介绍'}}</span>
        <span class="arrow-right"></span>
      </div>
      <span class="icon-closed" v-if="type===2"></span>
    </div>
  </div>
</template>

<script>
import utils from 'utils'
import ua from 'utils/ua'
export default {
  name: 'fund-cell',
  props: {
    type: {
      type: Number,
      required: true
    },
    fund: {
      type: Object,
      required: true
    },
  },
  data() {
    return {
      selected: '1'
    }
  },
  computed: {
    investAmount() {
      return utils.processNumber(this.fund.investAmount)
    },
    totalIncome() {
      return utils.processNumber(this.fund.totalIncome)
    },
    showTown() {
      let jcTenTown = this.fund.jcTenTown
      let townInfo = this.fund.townInfo
      if (!jcTenTown && !townInfo) {
        return false
      }
      if (jcTenTown && !townInfo) {
        if (!(jcTenTown.ecmName && jcTenTown.detailSchemeLink) && this.type === 2) {
          return false
        }
        if (!(jcTenTown.ecmName && jcTenTown.progressSchemeLink) && this.type === 1) {
          return false
        }
      }
      if (!jcTenTown && townInfo) {
        if (!(townInfo.ecmNo && townInfo.flyerUrl && townInfo.flyerId)) {
          return false
        }
      }
      return true
    },
  },
  mounted() {
    console.log('type', this.type)
    console.log('fund id', this.fund.id)
  },
  methods: {
    gotoDetail(fundId, fundName, fundCode) {
      // let path = `/private/hold/detail?fundId=${encodeURIComponent(fundId)}&fundName=${encodeURIComponent(fundName)}&fundCode=${encodeURIComponent(fundCode)}&type=${this.type}`
      // ua.jcy ? bridge.openWebView({
      //   url: getFullPath(path),
      //   auth: true,
      //   ylhAuth: true,
      //   newWebView: true
      // }) : this.$router.push({path: '/private/hold/detail', query: {fundId: encodeURIComponent(fundId), fundName: encodeURIComponent(fundName), fundCode: encodeURIComponent(fundCode), type: this.type}})
    },
    gotoTownIntro(jcTenTown, townInfo) {
      // if (jcTenTown) {
      //   let url = this.type === 1 ? jcTenTown.progressSchemeLink : jcTenTown.detailSchemeLink
      //   url && bridge.openScheme({
      //     url: url
      //   })
      // } else {
      //   if (townInfo.ecmNo && townInfo.flyerUrl && townInfo.flyerId) {
      //     let url = `${townInfo.flyerUrl}?town_number=${townInfo.flyerId}&ecmNo=${townInfo.ecmNo}`
      //     ua.jcy ? bridge.openWebView({
      //       url: url,
      //       auth: true,
      //       ylhAuth: true,
      //       newWebView: true
      //     }) : this.$router.push(url)
      //   }
      // }
    }
  }
}
</script>

<style lang="postcss" scoped>
  div.fund-cell {
    position: relative;
    background: #fff;
    margin-bottom: 5px;
    padding: 16px;
    h3 {
      margin: 0 0 10px 0;
      font-size: 14px;
      color: #666;
      .risk-level {
        display: inline-block;
        padding: 0 2px;
        border: 1Px solid #FD5729;
        border-radius: 0 5px 0 5px;
        font-size: 10px;
        color: #FD5729;
      }
    }
    .fund-intro {
      display: flex;
      text-align: center;
      .item-key {
        display: block;
        font-size: 12px;
        color: #aaa;
        height: 17px;
      }
      .item-value {
        display: block;
        font-size: 15px;
        color: #333;
        height: 40px;
        line-height: 40px;
      }
      .rate {
        width: 120px;
        .item-value {
          font-size: 28px;
          color: #FD5729;
          text-align: left;
        }
      }
      .invest {
        flex: 1;
      }
      .income {
        width: 93px;
        .item-value {
          color: #FD5729;
        }
      }
    }
    .fund-town {
      padding: 12px 0 0 0;
      margin: 16px 0 0 0;
      border-top: 1Px solid #eee;
      .townname {
        font-size: 14px;
        color: #FFA32A;
      }
      .townname::before {
        content: '';
        display: inline-block;
        width: 17px;
        height: 14px;
        margin: 0 8px 0 0;
        background: url('../../images/icon/town@2x.png') no-repeat;
        background-size: 100% 100%;
      }
      .arrow-right {
        float: right;
        display: inline-block;
        width: 6px;
        height: 12px;
        margin: 5px 0 0 0;
        background: url('../../images/icon/arrow-right@2x.png') no-repeat;
        background-size: 100% 100%;
      }
    }
    .icon-closed {
      position: absolute;
      top: 7px;
      right: 10px;
      display: block;
      width: 65px;
      height: 65px;
      background: url('../../images/closed@2x.png') no-repeat;
      background-size: 100% 100%;
    }
  }
</style>



