<template>
  <div class="index-main">
    <Header
    :fixed="fixed"
    :fullView="fullView"
    :right="right"
    :left="left"
    :opposition="opposition"
    @onLeft="popup"
    :customClass="customClass"
    v-show="jcy"
    >
    <span class="bold">{{indexInfo.appName}}</span>
    </Header>
    <mt-loadmore :top-method="loadTop" ref="loadmore" @top-status-change="handleTopChange">
      <div class="home-page-content" ref="homeContent">
        <!-- 一楼广告位 -->
        <div class="bannerOne banner-common" v-if="floor1Banners.length">
          <swiper :options="swiperBannerOption" ref="mySwiper">
            <swiper-slide v-for="(item, index) in floor1Banners" :key="index">
              <a href="javascript:;" @click="toOutLink({path: item.linkUrl, buryId: 2})">
                <div class="img-radius"> <img :src="item.imageUrl"></div>
              </a>
            </swiper-slide>
            <div v-show="floor1Banners.length > 1" class="swiper-pagination" slot="pagination"></div>
          </swiper>
        </div>
        <!-- 主要模块导航 -->
        <div class="columns-mian" ref="colums">
          <div class="columns-item" @click="routerLink({path:'/user/login',newWebView: true, auth: true, buryId: 3})">
            <div>
              <img src="./images/account.png" alt="">
            </div>
            <p>我的资产</p>
          </div>
          <div class="columns-item" @click="routerLink({path:'/score',newWebView: true, auth: true, ylhAuth: true, buryId: 4})">
            <div>
              <img src="./images/ylh.png" alt="">
            </div>
            <p>悦龙会</p>
          </div>
          <div class="columns-item" @click="routerLink({path:'/activity',newWebView: true, buryId: 5} )">
            <div>
              <img src="./images/find.png" alt="">
            </div>
            <p>发现精彩</p>
          </div>
          <div class="columns-item"  @click="toOutLink({path: (indexInfo.investmentInformation || {}).specialUrl || '#', hideHeader: true, buryId: 6})">
            <div>
              <img src="./images/sm.png" alt="">
            </div>
            <p>私募内参</p>
          </div>
        </div>
        <div class="notices" v-show="noticeList.length > 0">
          <swiper :options="swiperNoticeOption"  ref="swiperNotice" v-if="noticeList.length > 0">
            <swiper-slide v-for="(item, index) in noticeList" :key="index"><a  href="javascript:;"><p>{{item.title}}</p></a></swiper-slide>
          </swiper>
          <i class="icon right"></i>
        </div>
        <div class="normal-module town-top"></div>
        <!-- 小镇特色 -->
        <div class="town normal-module clearfix banner-common" v-if="townCharacteristic.length > 0">
          <h2>{{indexInfo.topOne}}<a class="more" href="javascript:;" @click="routerLink({path:'/town'})">更多</a></h2>
          <div class="town-list">
            <swiper :options="swiperTownOption">
              <swiper-slide v-for="(item, index) in townCharacteristic" :key="index">
                <a href="javascript:;" @click="routerLink({path: item.linkUrl})">
                  <div class="img-radius"> <img :src="item.imageUrl"></div>
                  <p>{{item.adName}}</p>
                </a>
              </swiper-slide>
              <div v-show="townCharacteristic.length > 2" class="swiper-pagination" slot="pagination"></div>
            </swiper>
          </div>
        </div>
        <!-- 发现精彩 -->
        <div class="find normal-module " v-if="operationTopic.length > 1">
          <h2>发现精彩<a class="more" href="javascript:;" @click="routerLink({path: '/activity'})">更多</a></h2>
          <div class="card-list bannerFind">
            <swiper :options="swiperFindOption">
              <swiper-slide v-for="(item, index) in operationTopic" :key="index">
                <a href="javascript:;">
                  <img :data-linkUrl="item.linkUrl" :src="item.coverUrl" alt="">
                </a>
              </swiper-slide>
            </swiper>
          </div>
        </div>
          <!-- 投研咨询 -->
        <div class="sm-consult normal-module" v-show="indexInfo.investmentInformation && investmentInformationList.length > 0">
          <h2>私募内参 <a class="more"  @click="toOutLink({path: (indexInfo.investmentInformation || {}).specialUrl, hideHeader: true, buryId: 11})" href="javascript:;" >更多</a></h2>
          <div class="sm-list">
            <div class="sm-item clearfix" v-for=" (item,index) in investmentInformationList" :key="index">
              <a href="javascript:;" @click="toOutLink({path: item.detailUrl, hideHeader: true})">
                <div class="sm-img"><img v-lazy="(item.images)[0]" alt=""></div>
                <div class="sm-text">
                  <h4>{{item.title}}</h4>
                  <p>
                    <span>{{item.channelName}} </span><span class="line">|</span> <span>{{item._openTime}}</span>
                    <span class="read-num">{{item.readCount}}</span>
                  </p>
                </div>
              </a>
            </div>
          </div>
        </div>
          <!-- 二楼广告位 -->
        <div class="banner-xhlj normal-module banner-common" v-if="floor2Banners.length > 0">
          <swiper :options="swiperBannerOption">
            <swiper-slide v-for="(item, index) in floor2Banners" :key="index">
              <a href="javascript:;" @click="routerLink({path: item.linkUrl, buryId: 12})">
                <div class="img-radius"><img :src="item.imageUrl" alt=""></div>
              </a>
            </swiper-slide>
            <div v-show="floor2Banners.length > 1" class="swiper-pagination" slot="pagination"></div>
          </swiper>

        </div>
          <!-- 积分专区 -->
        <div class="normal-module card clearfix score-card" v-show="integralArea.length > 0">
          <h2>积分商城<a href="javascript:;" class="more" @click="routerLink({path:'/score/mall',newWebView: true, auth: true, ylhAuth: true, buryId: 13})">更多</a></h2>
          <div class="card-list">
            <div :class="{'card-item':index > 0,'card-item-top':index == 0,' card-item-left': (index % 2 !== 0 && index > 0)}" v-for="(item, index) in integralArea" :key="index">
              <a href="javascript:;" @click="toOutAuthLink({path:item.linkUrl,newWebView: true, auth: true, ylhAuth: true, buryId: 14})">
                <div class="img-radius"><img  :src="item.imageUrl" alt=""></div>
                <p >{{item.adName}}</p>
              </a>
            </div>
          </div>
        </div>
      </div>
      <div slot="top">
        <TopStatus :status="topStatus"></TopStatus>
      </div>
    </mt-loadmore>
    <!-- 客服弹窗 -->
    <div class="pop-mask" v-show="popupVisible" @click="closePop">
      <div class="pop-layer">
        <div v-if="jcy" v-show="popupVisible" class="service">
          <ul>
            <li>
              <p @click="callHandle"><i class="icon icon-call"></i><span>联系客服</span></p>
            </li>
            <li>
              <p @click="routerLink({path:'/feedback',newWebView: true})" class="p-line"><i class="icon icon-edit"></i><span>意见反馈</span></p>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <div class="footer-info">
      <p>该页面由杭州金诚互联网金融服务有限公司设计并提供</p>
      <p><span class="footer-line"></span><span class="text-center">市场有风险 投资须谨慎</span><span class="footer-line"></span></p>
    </div>
  </div>
</template>
<script>
  import Header from 'components/Header'
  import {swiper, swiperSlide} from 'vue-awesome-swiper'
  import 'swiper/dist/css/swiper.css'
  import ua from 'utils/ua'
  import TopStatus from 'components/TopStatus'
  import scrollMonitor from 'utils/scroll-monitor'
  let vm = null
  export default {
    name: 'index',
    data() {
      return {
        watchNum: 0,
        topStatus: '',
        fixed: true,
        jcy: true,
        fullView: ua.jcy,
        right: {
          show: false
        },
        left: {
          show: true,
          icon: 'icon icon-call'
        },
        opposition: false,
        customClass: 'home-header',
        indexInfo: {},
        noticeList: [],
        operationTopic: [],
        floor1Banners: [{
        }],
        floor2Banners: [],
        investmentInformationList: [],
        townCharacteristic: [],
        integralArea: [],
        swiperOption: {
          slidesPerView: 1,
          autoplay: {
            delay: 2500,
            disableOnInteraction: false,
          },
          pagination: {
            el: '.swiper-pagination',
            clickable: true
          },
        },
        swiperNoticeOption: {
            direction: 'vertical',
            loop: true,
            observer: true,
            observeParents: true,
            autoplay: {
              delay: 3000,
              disableOnInteraction: false,
            },
            pagination: {
              el: '.notice-swiper',
              clickable: true,
            }
        },
        swiperBannerOption: {
          autoplay: {
            delay: 2500,
            disableOnInteraction: false,
          },
          pagination: {
            el: '.swiper-pagination',
            clickable: true,
          },
        },
        swiperTownOption: {
          slidesPerView: 2,
          spaceBetween: 7,
          slidesPerGroup: 2,
          pagination: {
            el: '.swiper-pagination',
            clickable: true,
          }
        },
        swiperFindOption: {
          effect: 'coverflow',
          initialSlide: 1,
          loop: true,
          grabCursor: true,
          centeredSlides: true,
          slidesPerView: 'auto',
          on: {
            click: function (e) {
              let path = e.target.dataset.linkurl || e.target.getAttribute('data-linkUrl')
              vm.routerLink({path});
            }
          },
          observer: true,
          observeParents: true,
          coverflowEffect: {
            rotate: 0,
            stretch: 200,
            depth: 100,
            modifier: 1,
            slideShadows: false,
          },
          pagination: {
            el: 'find-swiper',
            clickable: true,
          }
        },
        popupVisible: false,
        loginVisible: false
      }
    },
    components: {
      Header,
      swiper,
      swiperSlide,
      TopStatus
    },
    created() {
      vm = this;
    },
    mounted() {
      // 获取首页数据
      scrollMonitor((dir, scrollTop) => {
        // 滑动透明 ----> 显示
        if (dir === 'up' && scrollTop >= 10) {
          this.opposition = true
        } else if (dir === 'down' && scrollTop < 100) {
          this.opposition = false
        }
      })
      this.initFetch()
    },
    activated(){
      this.$refs.swiperNotice.update()
    },
    watch: {
      watchNum: {
        handler: function() {
          this.$refs.findSwiper && this.$refs.findSwiper.update && this.$refs.findSwiper.update()
        },
        deep: true
      }
    },
    methods: {
      initFetch(callback) {
        this.indexInfo = {
          appName: '逸理财',
          investmentInformation: {
            specialUrl: '/'
          },
          topOne: '特色小镇'
        }
        // 一楼广告位
        this.floor1Banners = [{
          linkUrl: '',
          imageUrl: 'https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=3748595743,2042953998&fm=26&gp=0.jpg'
        }, {
          linkUrl: '',
          imageUrl: 'https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=3389012736,1007124726&fm=26&gp=0.jpg'
        }, {
          linkUrl: '',
          imageUrl: 'https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=4152093700,520592336&fm=26&gp=0.jpg'
        }]
        // 通知
        this.noticeList = [{
          title: '我是通知标题',
          time: '2019-07-28'
        }]
        // 特色小镇
        this.townCharacteristic = [{
          linkUrl: '/town',
          imageUrl: 'https://5b0988e595225.cdn.sohucs.com/images/20170705/36914f2d96c34a86be2a9965a9f8e7cb.png',
          adName: '西湖龙坞茶镇'
        }, {
          linkUrl: '/town',
          imageUrl: 'https://5b0988e595225.cdn.sohucs.com/images/20170705/2233f8c01d0941dc876a898e25535d9f.png',
          adName: '运河财富小镇'
        }, {
          linkUrl: '/town',
          imageUrl: 'https://5b0988e595225.cdn.sohucs.com/images/20170705/76483facc5b34c94b2c41a8e93bb538c.png',
          adName: '临安云制造小镇'
        }, {
          linkUrl: '/town',
          imageUrl: 'https://5b0988e595225.cdn.sohucs.com/images/20170705/be3eca93cfa14a75ac4708e6a1376570.png',
          adName: '余杭梦栖小镇'
        }, {
          linkUrl: '/town',
          imageUrl: 'https://5b0988e595225.cdn.sohucs.com/images/20170705/c14d8e8be05a4d8abd5306cfe08711e0.png',
          adName: '滨江物联网小镇'
        }]
        // 发现精彩
        this.operationTopic = [{
          linkUrl: '/activity',
          coverUrl: 'https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=4093127378,1099634536&fm=26&gp=0.jpg'
        }, {
          linkUrl: '/activity',
          coverUrl: 'https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=59965771,3100329105&fm=26&gp=0.jpg'
        }, {
          linkUrl: '/activity',
          coverUrl: 'https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=2971000125,1507207318&fm=15&gp=0.jpg'
        }]
        // 私募内参
        this.investmentInformationList = [{
          detailUrl: '',
          images: ['https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=2250077022,109693851&fm=26&gp=0.jpg'],
          title: '三大股指齐回调 沪指失守3300点，水泥等周期股领涨两市',
          channelName: '投研资讯',
          _openTime: '2019-07-10',
          readCount: 120
        }, {
          detailUrl: '',
          images: ['https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=1655066693,2116016720&fm=26&gp=0.jpg'],
          title: '好买财富股权年度峰会：共话期2018新经济投资机会',
          channelName: '私募学堂',
          _openTime: '2019-07-08',
          readCount: 120
        }, {
          detailUrl: '',
          images: ['https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=579296799,2984637443&fm=26&gp=0.jpg'],
          title: '从此一行两会！为何只银监会保监会合并？看五大焦点',
          channelName: '投研资讯',
          _openTime: '2019-06-28',
          readCount: 120
        }]
        // 二楼广告位
        this.floor2Banners = [{
          linkUrl: '/invite',
          imageUrl: 'https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=4164724875,563665367&fm=11&gp=0.jpg'
        }, {
          linkUrl: '/invite',
          imageUrl: 'https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=2779675496,1252882551&fm=26&gp=0.jpg'
        }]
        // 积分商城
        this.integralArea = [{
          linkUrl: '',
          imageUrl: 'https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=1658810097,2551688508&fm=26&gp=0.jpg',
          adName: 'H12-iPhone X'
        }, {
          linkUrl: '',
          imageUrl: 'https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=1813392248,4273069575&fm=26&gp=0.jpg',
          adName: 'H10-beats耳机'
        }, {
          linkUrl: '',
          imageUrl: 'https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=1029060034,2889709665&fm=26&gp=0.jpg',
          adName: 'H11-海尔挂烫机'
        }]
        callback && callback()
      },
      handleTopChange(status) {
        this.topStatus = status
      },
      popup(){
        this.popupVisible = true
      },
      callHandle() {
        this.popupVisible = false
        // ua.jcy && bridge.callTel({
        //   tel: '4006881888'
        // })
        location.href= 'tel: 4006881888'
      },
      closePop() {
        this.popupVisible = false
      },
      routerLink(opt) {
        this.popupVisible = false
        this.$router.push(opt.path)
      },
      toOutLink(opt){
        location.href = opt.path
      },
      toOutAuthLink(opt){
        location.href = opt.path
      },
      loadTop(){
        this.initFetch(() => {
          this.$refs.loadmore && this.$refs.loadmore.onTopLoaded && this.$refs.loadmore.onTopLoaded()
        })
      }
    }
  }
</script>
<style>
.index-main {
    .banner-common{
      .swiper-pagination{
        bottom:5px;
        .swiper-pagination-bullet{
          opacity: 0.8;
          background: #FFFFFF;
          width:4px;
          height: 4px;
          border-radius:5px;
          margin:0 2px;
        }
        .swiper-pagination-bullet-active{
          width:15px;
        }
      }
    }
   .header{
      z-index: 9999;
      border-bottom: #fff !important;
      &.home-header {
        span {
          color:#FF541B;
        }
        .bold{
          font-size:20px;
          font-weight: bold;
          display: inline-block;
          margin-top: 4px;
        }
        .icon-call {
          display: inline-block;
          width:26px;
          height:26px;
          margin-left:8px;
          color:#666;
          margin-top:4px;
          background:url("./images/call-b.png") 0 0 no-repeat;
          background-size:26px;
        }
        &.opposition {
          span {
            color:#fff;
          }
          .icon-call {
            background: url("./images/call-b-re.png") 0 0 no-repeat;
            background-size: 26px;
          }
        }
      }
      .logo{
        width:72px;
        height: 26px;
      }
    }
}
</style>
<style scoped>
  .index-main {
    /* 此处定义当前页面样式 */
    background: #fff;
    .hide {
      text-align: center;
      padding-top: 300px;
      color: #999;
    }
    .normal-module{
      padding-bottom:15px;
      margin-bottom:15px;
      border-bottom:1Px solid #f6f6f6;
      h2{
        height: 28px;
        line-height: 28px;
        font-size: 20px;
        margin: 19px 0 8px;
        font-weight: 500;
        color: #333;
      }
      a.more{
        font-size:14px;
        color:#aaa;
        float: right;
      }
      &:last-child{
        border-bottom:none;
        margin-bottom:0;
      }
    }
    .img-radius{
      border-radius:5px;
      overflow: hidden;
    }
    .home-page-content{
      /*主要分类*/
      padding:0 20px;
      background: #fff;
      .columns-mian{
        height: 76px;
        display:flex;
        margin-top:16px;
        color:#666;
        .columns-item{
          width: 160px;
          height: 76px;
          box-sizing:border-box;
          font-size: 12px;
          flex: 1;
          text-align: center;
          img{
            width:40px;
            height:40px;
          }
          &.account{
            background:url("./images/account.png") center 0 no-repeat;
            background-size:40px;
          }
          &.yuelonhui{
            background:url("./images/ylh.png") center 0 no-repeat;
            background-size:40px;
          }
          &.find{
            background:url("./images/find.png") center 0 no-repeat;
            background-size:40px;
          }
          &.sm{
            background:url("./images/sm.png") center 0 no-repeat;
            background-size:40px;
          }

        }
      }
      /*通知*/
      .notices{
        height: 32px;
        line-height:32px;
        border-radius:16px;
        padding:0 30px 0 41px;
        margin-bottom:15px;
        background:url("./images/notice.png") #FFF0CD 15px 8px no-repeat;
        background-size:19px;
        position: relative;
        overflow: hidden;
        font-size: 13px;
        color: #FFA32A;
        .swiper-container{
          height: 32px;
        }
        a{
          display: block;
          color: #FFA32A;
        }
        .icon.right{
          position: absolute;
          right:10px;
          top:10px;
        }
        p{
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
      }
      .banner-common{
        .swiper-pagination{
          bottom:5px;
          .swiper-pagination-bullet{
            opacity: 0.8;
            background: #FFFFFF;
            width:4px;
            height: 4px;
            border-radius:5px;
            margin:0 2px;
          }
          .swiper-pagination-bullet-active{
            width:15px;
          }
        }
      }
      .bannerOne{
        width:100%;
        height: 150px;
        min-height: 150px;
        margin-top:9px;
        .swiper-wrapper{
          .swiper-slide{
            .img-radius{
              width:100%;
              height: 150px;
            }
            img{
              width:100%;
              height:150px;
            }

          }

        }

      }
      .town-top{
        padding-bottom:0;
      }
      /*特色小镇 积分 card类样式*/
      .town{
        padding-bottom:0;
        .town-list{
          width: 100%;
          .img-radius{
            height: 110px;
            margin-bottom:5px;
          }
          img{
            width:100%;
            height: 110px;

          }
          p{
            line-height:21px;
            font-size:15px;
            margin-bottom:24px;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
          }
          a{
            display:block;
            color:#333;
          }
        }
        .swiper-pagination{
          bottom:7px;
          .swiper-pagination-bullet{
            opacity: 0.8;
            background: #ddd;
            width:4px;
            height: 4px;
            border-radius:5px;
          }
          .swiper-pagination-bullet-active{
            width:15px;
          }
        }
      }
      .card{
        width: 100%;
        .card-list{
          i{
            width: 10px;
            height:8px;
            display: inline-block;
            background:url("./images/core.png") 0 0 no-repeat;
            background-size:10px 8px;
          }
          p{
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
          }
          .card-item-top{
            width: 100%;
            height:145px;
            margin-bottom:7px;
            position: relative;
            border-radius:5px;
            overflow: hidden;
            p{
              width:100%;
              height: 54px;
              background:url("./images/score_bg.png") 0 0 no-repeat;
              background-size:335px 55px;
              position:absolute;
              bottom:0px;
              left:0;
              padding: 26px 15px 8px 15px;
              box-sizing:border-box;
              font-size:15px;
              span{
                font-size:11px;
                float:right;
              }
            }
            a{
              display: block;

              color:#fff;
            }
            img{
              width:100%;
              height:145px;
            }
          }
          .card-item{
            border-radius:5px;
            overflow: hidden;
            &:first-child{
              margin-right:7px;
            }
            a{
              display:block;
              color:#333;
            }
            .img-radius{
              height: 110px;
              margin-bottom:5px;
            }
            img{
              width:100%;
              height: 110px;

            }
            p{
              line-height:21px;
              font-size:15px;
            }

            .score{
              font-size:11px;
              color:#666;
            }
          }
          .card-item-left{
            margin-right:7px;
          }
        }
        &.score-card{
          .card-list{
            .card-item{
              width:48.8%;
              float: left;
              .img-radius{
                overflow: hidden;
                height: 100px;
              }
            }
            img{
              width: 100%;
              height: 100px;

            }
            .card-item-top{
              img,a,.img-radius{
                height: 145px;
              }
            }
          }
        }
      }
      /*发现精彩*/
      .bannerFind{
        .swiper-slide{
          width: 270px;
          height:130px;
          a{
            display:block;
            margin: 0 auto;
            width:240px;
            height:130px;
            border-radius: 5px;
            overflow: hidden;
          }
          img{
            width:240px;
            height:130px;
          }
          &.swiper-slide-active{
            background:url("./images/shadows@2x.png") center center no-repeat;
            background-size:270px 130px;
          }

        }

      }
      /*私募内参*/
      .sm-consult{
        padding-bottom:0;
        .sm-list{
          margin-bottom:15px;
          .sm-item{
            padding-top:15px;
            &:first-child{
              padding-top:0;
            }
            &:last-child{
              border-bottom:none;
            }
            a{
              display:flex;
            }
            .sm-img{
              width: 100px;
              height: 75px;
              margin-right:10px;
              border-radius:5px;
              overflow: hidden;
              img{
                width: 100%;
                height: 75px;
              }
            }
            .sm-text{
              position: relative;
              height: 75px;
              flex:1;
              h4{
                line-height: 20px;
                font-size:15px;
                color:#333;
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
              }
              p{
                width:100%;
                font-size: 12px;
                line-height:17px;
                color:#aaa;
                position: absolute;
                bottom:0px;
                left:0;
                .line{
                  color:#E9E9E9;
                }
                .read-num{
                  float:right;
                  padding-left:20px;
                  background:url("./images/pv@2x.png") 2px 3px no-repeat;
                  background-size:14px 10px;
                }
              }
            }
          }
        }
      }
      .banner-xhlj{
        padding-bottom: 12px;
        .img-radius{
          height: 90px;
          border-radius:5px;
        }
        img{
          width:100%;
          height: 90px;
        }
      }
      .
      .score-card{
        border:none;
        margin-bottom:0;
      }
    }
    .footer-info{
      background:#f6f6f6;
      height: 80px;
      padding-top:18px;
      box-sizing: border-box;
      p{
        text-align:center;
        height: 16px;
        line-height:16px;
        margin-bottom:3px;
        font-size:11px;
        color:#AAA;
        .footer-line{
          display:inline-block;
          width:30px;
          height:1px;
          margin: 4px 0;
          background:#E9E9E9;
        }
        .text-center{
          margin: 0 6px;
        }
      }
    }
    .pop-mask {
      position: fixed;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      z-index: 999;
      background: rgba(0, 0, 0, 0.1);
      .pop-layer {
        position: relative;
      }
    }
    .service {
      position: absolute;
      top: 65px;
      left: 12px;
      z-index: 1000;
      background: #fff;
      width: 140px;
      transform: translate3d(0, 0, 0);
      border-radius: 4px;
      font-size:15px;
      box-sizing: border-box;
      color:#333;
      box-shadow: 0 0 3px 0 rgba(0,0,0,0.25);
      &::before {
        display: inline-block;
        width: 0;
        height: 0;
        border-left: 6px solid transparent;
        border-right: 6px solid transparent;
        border-bottom: 8px solid #fff;
        content: '';
        position: absolute;
        top: -8px;
        left: 15px;
        z-index: 1002;
      }
      li {
        padding: 0 20px;
        &.first-child {
          padding-top: 2px;
          border-radius: 4px 4px 0 0;
        }
        &:last-child{
          border-radius: 0 0 4px 4px;
        }
        &:hover {
          background: #d9d9d9;
        }
        p {
          height: 55px;
          line-height:55px;
          &.p-line {
            border-top: 1Px solid #f2f2f2;
          }
        }
      }
      li:hover {
        p.p-line {
          border-top: 1Px solid transparent;
        }
      }
      .icon {
        float: left;
        width:26px;
        height:26px;
        margin-right:9px;
        margin-top:14px;
        &.icon-call{
          background:url("./images/call-b.png") 0 0 no-repeat;
          background-size:25px;
          margin-top: 16px;
        }
        &.icon-edit{
          background:url("./images/edit.png") 1px 0 no-repeat;
          background-size:25px;
        }
      }
    }
  }
  @media only screen and (device-width: 375px) and (device-height: 812px) and (-webkit-device-pixel-ratio: 3) {
    .index-main {
      .header{
        &.home-header {
          .bold{
            margin-top: 10px;
          }
          .icon-call {
            margin-top: 10px;
          }
        }
      }
      .service {
        top: 83px;
        left: 15px;
      }
    }
  }
</style>



