<template>
  <div class="activity-ylhinvitation-main">
    <Header
      :fixed="fixed" 
      :fullView="fullView" 
      :right="right"
      @onLeft="goBack"
      :customClass="customClass"
    >
    <span class="bold">悦龙会邀请函</span>
    </Header>
    <div class="top"></div>
    <div class="order-box">
      <h2><img src="./images/title1@2x.png" alt="证监会监管多牌照财富管理机构" /></h2>
      <div class="order-form" ref="formOrder">
        <p>
          <i class="icon-name"></i>
          <input type="text" v-model="custName" placeholder="输入姓名" @blur="checkName">
        </p>
        <p>
          <i class="icon-tel"></i>
          <input type="number" v-model="mobileNo" placeholder="输入手机号码" @blur="checkMobileNo">
        </p>
        <p>
          <i class="icon-code"></i>
          <input type="text" v-model="smsCode" placeholder="输入验证码" @blur="checkCode">
          <span class="getcode blue" v-show="!isGetting" @click="getCode">获取验证码</span>
          <span class="getcode gray" v-show="isGetting">{{timeText}}</span>
        </p>
        <p ref="formBtn">
          <button class="btn radius" @click="goOrder" v-if="!btnLoading">立即预约咨询顾问</button>
          <button class="btn radius" :class="{btnLoading: btnLoading}" v-else>立即预约咨询顾问</button>
        </p>
      </div>
    </div>
    <div class="introtxt-box">
      <h2><img src="./images/title2@2x.png" alt="因为信任 所以选择" /></h2>
      <ul>
        <li>
          <i class="icon-brand"></i>
          <div>
            <h3>多牌照财富管理机构</h3>
            <p>基金销售许可证，六家已登记备案私募基金管理人，多牌照，高净值客户放心托付</p>
          </div>
        </li>
        <li>
          <i class="icon-asset"></i>
          <div>
            <h3>优质底层资产</h3>
            <p>资金和PPP项目建设合作，推动地方政府新型城镇化，目前已布局10个省份打造融合PPP＋产业化+金融化的投资、建设、运营的全生命链小镇</p>
          </div>
        </li>
        <li>
          <i class="icon-safe"></i>
          <div>
            <h3>严格风控体系 多层次安全把控</h3>
            <p>全方位风险评测，深入投前可研、投中运行、投后报告制度，防范化解产品风险</p>
          </div>
        </li>
      </ul>
    </div>
    <div class="introimg-box">
      <h2><img src="./images/title3@2x.png" alt="因为选择 所以尊贵" /></h2>
      <swiper :options="swiperOption" class="swiper-introimg">
        <swiper-slide>
          <img src="./images/swiper1@2x.png" alt="">
          <p>张家界天门仙境小镇、湖州亚运小镇等10多个特色小镇实地考察，与投资项目零距离现场接触</p>
        </swiper-slide>
        <swiper-slide>
          <img src="./images/swiper2@2x.png" alt="">
          <p>专属咨询顾问提供一对一专业服务，定制个性化财富管理方案</p>
        </swiper-slide>
        <swiper-slide>
          <img src="./images/swiper3@2x.png" alt="">
          <p>受邀金诚财富高端投资峰会与精英投资人共探新经济形势下的资产配置之道</p>
        </swiper-slide>
        <swiper-slide>
          <img src="./images/swiper4@2x.png" alt="">
          <p>悦龙会，高净值客户私享俱乐部，专为财富精英阶层打造高端社交平台</p>
        </swiper-slide>
      </swiper>
    </div>
    <div class="pop-mask" v-show="showPop">
      <div class="pop-layer">
        <div class="pop-top">提交结果</div>
        <div class="pop-content">{{message}}</div>
        <div class="pop-bottom">
          <a href="javascript:;" class="submit" @click="hidePop">确定</a>
        </div>
      </div>
    </div>
    <div class="btn-bottom" v-show="showBtnBottom"><button class="btn" @click="toFormOrder">立即预约咨询顾问</button></div>
    <loading cssText="background: #FFF; box-shadow: 0 0 0 1000px rgba(0,0,0,0.1);" :show="showLoading" ></loading>
  </div>
</template>
<script>
  import Header from 'components/Header'
  import {swiper, swiperSlide} from 'vue-awesome-swiper'
  import 'swiper/dist/css/swiper.css'
  import scrollMonitor from 'utils/scroll-monitor'
  import toast from 'utils/toast'
  import ua from 'utils/ua'
  import loading from 'components/Loading'
  
  export default {
    name: 'Invite',
    components: {
      swiper,
      swiperSlide,
      loading,
      Header
    },
    data() {
      return {
        fixed: true,
        fullView: true,
        right: {
          show: false
        },
        back: {
          show: true,
          text: '',
          icon: 'icon-back',
        },
        customClass: 'self-header',
        swiperOption: {
          slidesPerView: 'auto',
          resistanceRatio: 1,
          spaceBetween: 20,
          slidesOffsetBefore: 39,
          slidesOffsetAfter: 39,
          autoplay: {
            delay: 3000,
            disableOnInteraction: false
          },
          // loop: true,
        },
        custName: '',
        mobileNo: '',
        smsCode: '',
        showBtnBottom: false,
        isGetting: false,
        timeText: '',
        showPop: false,
        message: '',
        btnLoading: false,
        showLoading: false,
        timer: null,
      }
    },
    mounted() {
      scrollMonitor(() => {
        if (!this.$refs.formBtn) return
        let formBtnBottom = this.$refs.formBtn.getBoundingClientRect().bottom
        if (formBtnBottom <= 0) {
          this.showBtnBottom = true
        } else {
          this.showBtnBottom = false
        }
      })
      this.initShare()
    },
    computed: {
    },
    methods: {
      toFormOrder() {
        window.scrollTo(0, 515)
      },
      clearForm() {
        this.custName = ''
        this.mobileNo = ''
        this.smsCode = ''
      },
      clearTimer() {
        this.isGetting = false
        clearInterval(this.timer)
      },
      hidePop() {
        this.showPop = false
      },
      goOrder() {
        // 再次校验
        if (!this.custName) {
          toast('姓名输入为空')
          return
        }
        const RegPhone = /^1\d{10}$/
        if (!this.mobileNo) {
          toast('手机号输入为空')
          return
        } else if (!RegPhone.test(this.mobileNo)) {
          toast('手机号码不是11位数字')
          return
        }
        if (!this.smsCode) {
          toast('验证码输入为空')
          return
        } else if (this.smsCode.length !== 6) {
          toast('验证码不是6位字符')
          return
        }
        // 提交数据
        this.btnLoading = true
        this.showLoading = true
        // api.invitationCreateLead({
        //   custName: this.custName,
        //   mobileNo: this.mobileNo,
        //   smsCode: this.smsCode,
        //   brokerNo: this.brokerNo,
        //   shareCustNo: this.shareCustNo
        // }).then(res => {
        //   if (res.code === 0) {
        //     this.message = '信息已成功提交，咨询顾问会尽快与您联系'
        //     this.showPop = true
        //     this.clearForm()
        //     this.clearTimer()
        //   } else if (res.code === 20102) {
        //     toast(res.errmsg)
        //     this.smsCode = ''
        //   } else if (res.code === 18016) {
        //     this.message = '您已注册悦龙会，请勿重复提交'
        //     this.showPop = true
        //     this.clearForm()
        //     this.clearTimer()
        //   } else if (res.code === 18017) {
        //     this.message = '您已有咨询顾问，请勿重复提交'
        //     this.showPop = true
        //     this.clearForm()
        //     this.clearTimer()
        //   } else if (res.code === 18018) {
        //     this.message = '您已提交过信息，请勿重复提交'
        //     this.showPop = true
        //     this.clearForm()
        //     this.clearTimer()
        //   } else {
        //     this.message = res.errmsg
        //     this.showPop = true
        //   }
        //   this.btnLoading = false
        //   this.showLoading = false
        // }).catch(() => {
        //   this.message = '系统异常，请稍后再试'
        //   this.showPop = true
        //   this.btnLoading = false
        //   this.showLoading = false
        // })
      },
      getCode() {
        const regPhone = /^1\d{10}$/
        if (!this.mobileNo) {
          toast('请先填写手机号码')
          return
        } else if (!regPhone.test(this.mobileNo)) {
          toast('手机号码填写有误')
          return
        }
        if (this.smsCode && !this.isGetting) {
          this.smsCode = ''
        }
        if (this.isGetting) {
          return
        }
        this.timeText = '重新获取 60s'
        this.isGetting = true
        let repeat = 59
        this.timer = setInterval(() => {
          if (!repeat) {
            clearInterval(this.timer)
          }
          this.timeText = `重新获取 ${repeat--}s`
        }, 1000)
        setTimeout(() => {
          this.isGetting = false
          clearInterval(this.timer)
        }, 60000)
        this.sendCode(() => {
          this.isGetting = false
          clearInterval(this.timer)
        })
      },
      sendCode(callback) {
        // api.sendSms({
        //   mobileNo: this.mobileNo
        // }).then(res => {
        //   if (res.code === 0) {
        //     toast('验证码已发送')
        //   } else {
        //     callback && callback()
        //     toast('验证码发送失败')
        //   }
        // })
      },
      checkName() {
        if (!this.custName) {
          toast('姓名输入为空')
        }
      },
      checkMobileNo() {
        const regPhone = /^1\d{10}$/
        if (!this.mobileNo) {
          toast('手机号输入为空')
        } else if (!regPhone.test(this.mobileNo)) {
          toast('手机号码不是11位数字')
        }
      },
      checkCode() {
        if (!this.smsCode) {
          toast('验证码输入为空')
        } else if (this.smsCode.length !== 6) {
          toast('验证码不是6位字符')
        }
      },
      initShare() {
        // let shareInfo = {
        //   title: '金诚财富诚邀您共享财富人生',
        //   navTitle: '金诚财富诚邀您共享财富人生',
        //   desc: 'PPP发展私募基金为投资者开辟财富管理新思路',
        //   subTitle: 'PPP发展私募基金为投资者开辟财富管理新思路',
        //   imgUrl: 'http://jcyimg.jcease.com/FvB4ppoRfTrwrIcOBohlx2hJp4rY',
        //   image: 'http://jcyimg.jcease.com/FvB4ppoRfTrwrIcOBohlx2hJp4rY',
        //   link: location.href,
        //   success() {
        //     recordBuryPoint(31, ua.deviceName)
        //   }
        // }
        // if (!ua.jcy) {
        //   wxBridge.ready(() => {
        //     wx.onMenuShareAppMessage(shareInfo)
        //     wx.onMenuShareTimeline(shareInfo)
        //   })
        // } else {
        //   bridge.onShare(shareInfo)
        // }
      },
       goBack() {
        this.$router.back()
      }
    }
  }
</script>
<style>
  body {
    min-height: 160px;
    background: url('./images/bgBottom@2x.png') left bottom no-repeat;
    background-size: contain;
  }
</style>

<style scoped>
    .activity-ylhinvitation-main {
      /* 此处定义当前页面样式 */
      .top {
        background: url('./images/bgTop@2x.png') no-repeat;
        background-size: 100% 100%;
        height: 490px;
        margin-bottom: 40px;
      }
      h2 {
        img {
          width: 100%;
        }
      }
      .btn {
        width: 100%;
        height: 50px;
        background-image: linear-gradient(180deg, #6C63FF 0%, #2A52C2 100%);
        border: 0;
        color: #fff;
        font-size: 17px;
        font-weight: 500;
        &.radius {
          border-radius: 5px;
        }
        &.btnLoading {
          opacity: 0.5;
        }
      }
      .order-box {
        .order-form {
          margin: 32px 28px 54px;
          p {
            position: relative;
            i {
              position: absolute;
              top: 12px;
              left: 15px;
              display: inline-block;
              width: 25px;
              height: 25px;
              &.icon-name {
                background: url('./images/icon-user@2x.png') no-repeat;
                background-size: 100% 100%;
              }
              &.icon-tel {
                background: url('./images/icon-tel@2x.png') no-repeat;
                background-size: 100% 100%;
              }
              &.icon-code {
                background: url('./images/icon-code@2x.png') no-repeat;
                background-size: 100% 100%;
              }
            }
            .getcode {
              position: absolute;
              top: 12px;
              right: 0;
              display: inline-block;
              width: 116px;
              text-align: center;
              font-size: 15px;
              border-left: 1Px solid #eee;
              &.blue {
                color: #2A52C2;
              }
              &.gray {
                color: #aaa;
              }
            }
          }
          input {
            width: calc(100% - 66px);
            height: 45px;
            padding: 0 13px 0 53px;
            background: #FAFAFA;
            border: 1px solid #EEEEEE;
            border-radius: 5px;
            margin-bottom: 9px;
            font-size: 15px;
            color: #333;
            &::-webkit-input-placeholder {
              font-size: 15px;
              color: #D4D4D4;
            }
          }
        }
      }
      .introtxt-box {
        ul {
          margin: 40px 26px 40px 20px;
          li {
            margin-bottom: 30px;
            display: flex;
            &:last-child {
              margin-bottom: 0;
              h3 {
                font-size: 16px;
                margin-bottom: 2px;
              }
            }
            i {
              width: 100px;
              height: 100px;
              margin: -10px 10px 0 0;
              &.icon-brand {
                background: url('./images/icon-brand@2x.png') no-repeat;
                background-size: 100% 100%;
              }
              &.icon-asset {
                background: url('./images/icon-asset@2x.png') no-repeat;
                background-size: 100% 100%;
              }
              &.icon-safe {
                background: url('./images/icon-safe@2x.png') no-repeat;
                background-size: 100% 100%;
              }
            }
            div {
              flex: 1;
              h3 {
                font-size: 17px;
                color: #333;
                font-weight: 500;
                margin-bottom: 1px;
              }
              p {
                font-size: 13px;
                color: #9D9D9D;
                line-height: 18px;
              }
            }
          }
        }
      }
      .introimg-box {
        margin-bottom: 144px;
        .swiper-introimg {
          margin-top: 35px;
          .swiper-slide {
            width: auto !important;
          }
          img {
            width: 298px;
          }
          p {
            width: 255px;
            padding-left: 20px;
            font-size: 13px;
            color: #333;
            line-height: 18px;
            margin-top: 20px;
          }
        }
      }
      .btn-bottom {
        position: fixed;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 50px;
        z-index: 2;
        background: #fff;
      }
      .pop-mask {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(55,55,55,.5);
        z-index: 2002;
        display: flex;
        align-items: center;
        justify-content: center;
        .pop-layer {
          width: 260px;
          background-color: #fff;
          border-radius: 5px;
          text-align: center;
          .pop-top {
            font-size: 17px;
            font-weight: 500;
            color: #333;
            margin: 18px 0 10px;
          }
          .pop-content {
            font-size: 15px;
            color: #666;
            line-height: 22px;
            margin-bottom: 20px;
            padding: 0 20px;
          }
          .pop-bottom {
            border-top: 1Px solid #eee;
            font-size: 16px;
            display: flex;
            a {
              flex: 1;
              display: block;
              padding: 12px 0;
            }
            a.submit {
              color: #2A52C2;
            }
          }
        }
      }
    }
    @media only screen and (device-width: 375px) and (device-height: 812px) {
      .activity-ylhinvitation-main {
        .btn-bottom {
          padding-bottom: 34px;
        }
      }
    }
</style>


