<template>
  <li class="trade-cell" >
    <p class="type">{{trade.tradeType}}</p>
    <p class="fund">
      {{trade.productName}}
      ({{trade.productCode}})
      <span class="time">{{tradeTime}}</span>
    </p>
    <p class="money"><span>{{tradeAmount}}</span>元</p>
  </li>
</template>

<script>
import moment from 'moment'
import utils from 'utils'
export default {
  name: 'trade-cell',
  props: {
    trade: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
    }
  },
  computed: {
    tradeTime() {
      return this.trade.tradeTime && moment(this.trade.tradeTime).format('YYYY-MM-DD')
    },
    tradeAmount() {
      return this.trade.tradeAmount && utils.processNumber(this.trade.tradeAmount)
    }
  },
  methods: {
  }
}
</script>

<style lang="postcss">
  .trade-cell {
    background: #fff;
    border-bottom: 1Px solid #f6f6f6;
    padding: 15px;
    display: flex;
    .type {
      font-size: 15px;
      color: #333;
      width: 45px;
    }
    .fund {
      flex: 1;
      font-size: 14px;
      color: #666;
      .time {
        display: block;
        margin-top: 8px;
        font-size: 12px;
        color: #999;
      }
    }
    .money {
      width: 30%;
      text-align: right;
      font-size: 12px;
      color: #000;
      span {
        font-size: 16px;
        color: #666;
        white-space: normal;
        word-wrap: break-word;
      }
    }
  }
</style>



