<template>
  <div class="footer-wrapper" :class="{fixed:fixed}">
    <div slot="footer" class="footer">
      <mt-tabbar v-model="current" v-if="footerType==='tab'">
        <mt-tab-item :id="btn.link" v-for="(btn,index) in btns" :key="index"  @click="testClick(btn.link)">
          <div v-if="btn.icon" :class="[{'icon-btn':!btn.title},'item-icon']"><i :class="btn.icon"></i></div>
          <a :class="{'link-btn': !btn.icon}" v-if="btn.title">{{btn.title}}</a>
        </mt-tab-item>
      </mt-tabbar>
      <slot name="otherType" v-else></slot>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'footer2',
    props: {
      footerType: {
        type: String,
        'default': 'tab'
      },
      selectTab: {
        type: String,
        'default': 'demo'
      },
      fixed: {
        type: Boolean,
        'default': true
      },
      btns: {
        type: Array,
        'default': function(){
          return [
            {
              type: 'tab1',
              icon: 'icon right'
            },
            {
              type: 'tab2',
              icon: 'icon share-p'
            },
            {
              type: 'tab3',
              icon: 'icon left'
            }
          ]
        }
      }
    },
    data() {
      return {
        current: ''
      }
    },
    watch: {
      current: function (val, oldVal) {
        // 这里就可以通过 val 的值变更来确定
        if (!oldVal) {
          return false
        }
        this.$router.push(val)
      }
    },
    mounted (){
      this.current = this.selectTab;
    },
    computed: {},
    methods: {
    },
  }
</script>
<style lang="postcss" scoped>
.footer-wrapper{
  height: 46px;
  line-height: 46px;
  background: #fafafa;
  &.fixed{
    position: fixed;
    bottom:0;
    left:0;
    right:0;
    z-index: 2000;
  }
  .footer{
    height: 46px;
    .item-btn{
      display: block;
      height: 46px;
      flex:1;
      -webkit-box-flex: 1;
      text-align: center;
      color:#333;
      &.active{
        color:red;
      }
    }
    a{
      color:#333;
    }
    .item-icon{
      height: 24px;
      i{
        width: 22px;
        height: 22px;
        font-size:22px;
      }
    }
    .icon-btn{
      height: 46px;
      line-height:46px;
      
      i{
        width: 46px;
        height: 46px;
        font-size:30px;
      }
    }
    .link-btn{
      display: inline-block;
      height: 46px;
      line-height: 34px;
      font-size: 18px;
    }
    .text{
      font-size: 12px;
      line-height: 1;
    }
    .is-selected{
      a,i.icon{
        color:red;
      }
      
    }
    .mint-tabbar > .mint-tab-item.is-selected{
      background: #fafafa;
    }
    .mint-tabbar{
      background-size: 100% 0;
      position:static;
    }
    .footerText{
      vertical-align: middle;
      padding: 0 20px;
      img{
        width:36px;
        height: 36px;
        margin-top:5px;
        float:right;
      }
    }
  }
  
}
@media only screen
and (device-width: 375px)
and (device-height: 812px)
and (-webkit-device-pixel-ratio: 3) {
  .footer-wrapper{
    padding-bottom:34px;
    .footer{
      .mint-tabbar{
        bottom: 34px;
      }
    }
  }
}
</style>


