<template>
  <div class="clearfix upload-layout-m">
    <div class="upload-list clearfix" >
      <div class="upload-list-item" v-for="(item,index) in imagesList" :key="index" >
        <div class="img-radius">
          <img :src="item" alt="" >
        </div>
        <mt-progress :value="value" v-if="progressVisible && index == imagesList.length-1" transition="progress-fade">
          <div slot="end"></div>
        </mt-progress>
        <a href="javascript:;" class="remove icon show-delete" @click="removeImages(index)"></a>
      </div>
    </div>
     <!-- <input type="file" capture="camera" multiple="multiple" accept="image/gif,image/jpeg,image/bmp" > -->
    <div class="upload_button" v-show="!isFull" @click="onFile">
      <input ref="upFile" id="file" name="file" class="hidden" type="file" @change="upload" />
    </div>
  </div>
</template>
<script>
  import { EXIF } from 'exif-js'
  import toast from 'utils/toast'
  export default {
    name: 'upload',
    props: {
      size: {
        type: Number,
        default(){
          return 5
        }
      },
      length: {
        type: Number,
        default(){
          return 3
        }
      },
      imagesList: {
        type: Array,
        default(){
          return []
        }
      },
      onGetToken: {
        type: Function,
        default() {
          console.log('获取token')
        }
      },
      onSuccess: {
        type: Function,
        default() {
          console.log('上传成功')
        }
      },
      onRemove: {
        type: Function,
        default() {
          console.log('删除成功')
        }
      }
    },
    data(){
      return {
        domain: '',
        isFull: false,
        isDelete: false,
        progressVisible: false,
        value: 0,
      }
    },
    watch: {
      imagesList: function(){
        this.isShowButton()
      }
    },
    computed: {},
    mounted() {
      document.addEventListener('click', () => {
        this.isDelete = false
      })
    },
    methods: {
      onFile(){
        if (this.isUploading) {
          return
        }
        this.$refs.upFile.click();
      },
      beforeVerify(file){
        const isJPG = file.type.indexOf('image') !== -1;
        if (!isJPG) {
          toast('图片格式错误');
        }
        const isLt5M = file.size / 1024 / 1024 < this.size;
        if (!isLt5M) {
          toast(`图片大小不能超过${this.size}M`);
        }
        return isJPG && isLt5M;
      },
      upload(e){
        this.isDelete = false
        this.isUploading = true
        this.file = e.target;
        let files = e.target.files || e.dataTransfer.files;
        if (!files.length) return;
        let picFile = files[0];
        // 图片大小格式校验
        if (!this.beforeVerify(picFile)) {
          this.isUploading = false
          return;
        }
        this.imgPreview(picFile);
      },
      imgPreview(picFile) {
        this.value = 0;
        this.progressVisible = true;
        this.imagesList.push('')
        this.timer = setInterval(() => {
          this.value++
          if (this.value === 90) {
            clearInterval(this.timer)
          }
        }, 10);
        //去获取拍照时的信息，解决拍出来的照片旋转问题
        let Orientation;
        EXIF.getData(picFile, () => {
          Orientation = EXIF.getTag(picFile, 'Orientation');
          // alert(Orientation + ' Orientation')
        });
        // 看支持不支持FileReader
        if (!picFile || !window.FileReader) return;
        if (/^image/.test(picFile.type)) {
          // 创建一个reader
          let reader = new FileReader();
          // 将图片2将转成 base64 格式
          reader.readAsDataURL(picFile);
          // 读取成功后的回调
          reader.onloadend = () => {
            let result = reader.result;
            let img = new Image();
            img.src = result;
            //判断图片是否大于100K,是就直接上传，反之压缩图片
            let picResult = ''
            if (result.length <= (100 * 1024)) {
              picResult = result;
              this.postImg(picResult);
            } else {
              img.onload = () => {
                let data = this.compress(img, Orientation);
                picResult = data;
                this.postImg(picResult);
              }
            }
          }
        }
      },
      postImg(picResult) {
        //这里写接口
        let tempStr = picResult.substr(11, 3)
        let picData = ''
        if (tempStr === 'png') {
          picData = picResult.substring(22)
        } else if (tempStr === 'jpe') {
          picData = picResult.substring(23)
        }
        //先从自己的服务端拿到token
        let promise = () => {
          return new Promise((resolve, reject) => {
            this.onGetToken(resolve, reject)
          })
        }
        let gettoken = async () => {
          try {
            let aa = await promise()
            this.domain = aa.domain
            let config = {
              headers: {
                'Content-Type': 'application/octet-stream',
                'Authorization': `UpToken ${aa.token}`
              }
            }
            this.uploading(picData, config)
          } catch (e) {
            e();
          }
        }
        gettoken()
      },
      uploading(picData, config){
        axios.post('//up.qiniup.com/putb64/-1', picData, config).then(response => {
          if (response.data && response.data.key) {
            // 清空file的值
            this.file.value = ''
            let url = this.domain + '/' + response.data.key
            // 触发file事件
            this.isUploading = false
            // this.isShowButton()
            this.onSuccess(url)
            this.imgOnload(url)
            clearInterval(this.timer)
          }
        }).catch(() => {
          console.log('fail')
        })
      },
      imgOnload(src){
        let newImg = new Image()
        newImg.src = src
        newImg.onload = () => { // 图片加载成功后把地址给原来的img
          this.url = newImg.src
          this.imagesList[this.imagesList.length - 1] = newImg.src
          this.progressVisible = false
        }
      },
      rotateImg(img, direction, canvas) {
        //最小与最大旋转方向，图片旋转4次后回到原方向
        const minStep = 0;
        const maxStep = 3;
        if (img == null) return;
        //img的高度和宽度不能在img元素隐藏后获取，否则会出错
        let height = img.height;
        let width = img.width;
        let step = 2;
        if (step == null) {
          step = minStep;
        }
        if (direction === 'right') {
          step++;
          //旋转到原位置，即超过最大值
          step > maxStep && (step = minStep);
        } else {
          step--;
          step < minStep && (step = maxStep);
        }
        //旋转角度以弧度值为参数
        let degree = step * 90 * Math.PI / 180;
        let ctx = canvas.getContext('2d');
        switch (step) {
          case 0:
            canvas.width = width;
            canvas.height = height;
            ctx.drawImage(img, 0, 0);
            break;
          case 1:
            canvas.width = height;
            canvas.height = width;
            ctx.rotate(degree);
            ctx.drawImage(img, 0, -height);
            break;
          case 2:
            canvas.width = width;
            canvas.height = height;
            ctx.rotate(degree);
            ctx.drawImage(img, -width, -height);
            break;
          case 3:
            canvas.width = height;
            canvas.height = width;
            ctx.rotate(degree);
            ctx.drawImage(img, -width, 0);
            break;
        }
      },
      compress(img, Orientation) {
        let canvas = document.createElement("canvas");
        let ctx = canvas.getContext('2d');
          //瓦片canvas
        let tCanvas = document.createElement("canvas");
        let tctx = tCanvas.getContext("2d");
        let initSize = img.src.length;
        let width = img.width;
        let height = img.height;
        //如果图片大于四百万像素，计算压缩比并将大小压至400万以下
        let ratio;
        if ((ratio = width * height / 4000000) > 1) {
          console.log("大于400万像素")
          ratio = Math.sqrt(ratio);
          width /= ratio;
          height /= ratio;
        } else {
          ratio = 1;
        }
        canvas.width = width;
        canvas.height = height;
        // 铺底色
        ctx.fillStyle = "#fff";
        ctx.fillRect(0, 0, canvas.width, canvas.height);
        //如果图片像素大于100万则使用瓦片绘制
        let count;
        if ((count = width * height / 1000000) > 1) {
          console.log("超过100W像素");
          count = ~~(Math.sqrt(count) + 1); //计算要分成多少块瓦片
          // 计算每块瓦片的宽和高
          let nw = ~~(width / count);
          let nh = ~~(height / count);
          tCanvas.width = nw;
          tCanvas.height = nh;
          for (let i = 0; i < count; i++) {
            for (let j = 0; j < count; j++) {
              tctx.drawImage(img, i * nw * ratio, j * nh * ratio, nw * ratio, nh * ratio, 0, 0, nw, nh);
              ctx.drawImage(tCanvas, i * nw, j * nh, nw, nh);
            }
          }
        } else {
          ctx.drawImage(img, 0, 0, width, height);
        }
        //修复ios上传图片的时候 被旋转的问题
        if (Orientation !== "" && Orientation !== 1){
          switch (Orientation) {
            case 6://需要顺时针（向左）90度旋转
              this.rotateImg(img, 'left', canvas);
              break;
            case 8://需要逆时针（向右）90度旋转
              this.rotateImg(img, 'right', canvas);
              break;
            case 3://需要180度旋转
              this.rotateImg(img, 'right', canvas);//转两次
              this.rotateImg(img, 'right', canvas);
              break;
          }
        }
        //进行最小压缩
        let ndata = canvas.toDataURL('image/jpeg', 0.1);
        console.log('压缩前：' + initSize);
        console.log('压缩后：' + ndata.length);
        console.log('压缩率：' + ~~(100 * (initSize - ndata.length) / initSize) + "%");
        tCanvas.width = tCanvas.height = canvas.width = canvas.height = 0;
        return ndata;
      },
      isShowButton(){
        this.isFull = this.imagesList.length >= this.length
      },
      // showDeleteButton(id, event){
      //   event.stopPropagation();
      //   event.preventDefault();
      //   clearInterval(this.timer);
      //   this.timer = setTimeout(() => {
      //     this.isDelete = true
      //   }, 800)
      // },
      removeImages(index){
        let promise = () => {
          return new Promise((resolve, reject) => {
            this.onRemove({resolve, reject, index})
          })
        }
        let remove = async () => {
          try {
            await promise()
            this.imagesList.splice(index, 1);
          } catch (e) {
            // 取消删除
            this.isDelete = false
          }
        }
        remove()
      },
      // touchend(){
      //   clearTimeout(this.timer);
      //   return false
      // },
      destroyed() {
        document.removeEventListener('click')
      },
    }
  }
</script>
<style lang="postcss">
  .upload-list-item{
    .mt-progress{
      .mt-progress-content{
        .mt-progress-progress{
          background: #fd5729;
        }
      }
    }
  }
</style>

<style lang="postcss" scoped>
  .upload-layout-m{
    .hidden{
      display: none;
    }
    .upload-list,.upload_button{
      float: left;
    }
    .upload-list-item{
      float: left;
      margin-right:18px;
      position: relative;
      .mt-progress{
        position:absolute;
        width: 81px;
        height: 81px;
        background: #fff;
        top:0;
        left:0;
        border-radius:5px;
        border:1px solid #d9d9d9;
        .mt-progress-content{
          .mt-progress-progress{
            background: #fd5729;
          }
        }
      }
      .img-radius{
        width:85px;
        height: 85px;
        border-radius:5px;
        overflow: hidden;
        border:1px solid #fff;
        box-sizing:border-box;
        display:flex;
        align-items:center;;
      }
      img {
        width:100%;
      }
      a{
        position:absolute;
        top:-10px;
        right: -10px;
        width: 20px;
        height: 20px;
        border-radius:50%;
        background: #f2f3fe;
        text-align: center;
        line-height: 20px;
        z-index: 10;
        background: url("./images/delete.png") 0 0 no-repeat;
        background-size:100%; 
        opacity: 0;
        &.show-delete{
          opacity: 1;
          transition: opacity 0.4s;
        }
      }
    }
    .upload_button{
      width:85px;
      height:85px;
      line-height: 85px;
      text-align: center;
      color:#ccc;
      font-size:40px;
      background:url('./images/btn.png') #fff 0 0 no-repeat;
      background-size:85px;
      padding: 2px;
      box-sizing: border-box;
      overflow: hidden;
      
    }
    
   
  }

</style>


