import Vue from 'vue'
import Router from 'vue-router'
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
import Index from '@/pages/Index/Index'
import Town from '@/pages/Town/Index'
import Activity from '@/pages/Activity/Index'
import Score from '@/pages/Score/Index'
import Mall from '@/pages/Score/Mall/Index'
import Login from '@/pages/User/Login/Index'
import Account from '@/pages/User/Account/Index'
import Trade from '@/pages/User/Account/Trade/Index'
import Faq from '@/pages/Faq/Index'
import Invite from '@/pages/Invite/Index'
import Feedback from '@/pages/Feedback/Index'
import Private from '@/pages/Private/Hold/Index'

Vue.use(Router)
Vue.use(MintUI)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/town',
      name: 'Town',
      component: Town
    },
    {
      path: '/activity',
      name: 'Activity',
      component: Activity
    },
    {
      path: '/score',
      name: 'Score',
      component: Score
    },
    {
      path: '/score/mall',
      name: 'Mall',
      component: Mall
    },
    {
      path: '/user/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/user/account',
      name: 'Account',
      component: Account
    },
    {
      path: '/user/account/trade',
      name: 'Trade',
      component: Trade
    },
    {
      path: '/faq',
      name: 'Faq',
      component: Faq
    },
    {
      path: '/invite',
      name: 'Invite',
      component: Invite
    },
    {
      path: '/feedback',
      name: 'Feedback',
      component: Feedback
    },
    {
      path: '/private/hold',
      name: 'Private',
      component: Private
    }
  ]
})



