        <mt-tab-item id="2">已结清资产</mt-tab-item>
      </mt-navbar>
    </div>
    <mt-loadmore :top-method="loadTop" @top-status-change="handleTopChange" ref="loadmore">
      <div :style="windowH">
        <div class="asset-overview">
          <p class="total">
            <span class="item-key">总资产(元)</span>
            <span class="item-value">{{privateAsset}}</span>
          </p>
          <div class="income">
            <p>
              <span class="item-key">已结清资产总额(元)</span>
              <span class="item-value">{{closedAsset}}</span>
            </p>
