            <span>{{riskLevel}}进取型</span>
          </div>
          <div class="level-bg"></div> -->
        </div>
        <div class="asset-detail">
          <div>
            <mt-navbar class="tab-nav" ref="navB"  v-model="selected">
              <mt-tab-item id="1">存续资产</mt-tab-item>
              <mt-tab-item id="2">已结清资产</mt-tab-item>
            </mt-navbar>
          </div>
          <mt-tab-container v-model="selected">
            <mt-tab-container-item id="1">
              <div v-if="fundList.length">
                <div is="fund-cell" v-for="(item, index) in fundList" :key="index" :fund="item" :type="type"></div>
              </div>
              <div v-if="!fundList.length && isLoaded" class="noData">
                <p>未有持仓</p>
              </div>
            </mt-tab-container-item>
            <mt-tab-container-item id="2">
              <div v-if="fundList.length">
                <div is="fund-cell" v-for="(item, index) in fundList" :key="index" :fund="item" :type="type"></div>
              </div>
              <div v-if="!fundList.length && isLoaded" class="noData">
                <p>未有持仓</p>
              </div>
            </mt-tab-container-item>
          </mt-tab-container>
        </div>
      </div>
      <div slot="top">
        <TopStatus :status="topStatus"></TopStatus>
      </div>
    </mt-loadmore>
  </div>
</template>
