</script>
<style>
  .private-hold-main {
    .header-wrapper .header {
      &.self-header {
        background-color: #FD5729;
        border: none;
        color: #fff;
        font-size: 17px;
        i.left{
          line-height: 45px;
          font-size: 28px;
          color:#fff;
          margin-top:10px;
        }
      }
    }
    .mint-navbar .mint-tab-item {
        padding: 12px 0;
      }
      .mint-tab-item-label {
        font-size: 15px;
        color: #333;
      }
      .mint-navbar .mint-tab-item.is-selected {
        border-bottom: 3px solid #FF5A23;
        margin-bottom:0px;
        .mint-tab-item-label {
          color: #FF5A23;
        }
      }
    .fixed{
        width:100%;
        position: fixed;
        z-index: 2;
        top:0;
        left:0;
        .tab-nav {
          padding-top: 28px;
          margin-bottom: 10px;
        }
        .mint-navbar .mint-tab-item {
          padding: 12px 0;
        }
        .mint-tab-item-label {
          font-size: 15px;
          color: #333;
        }
        .mint-navbar .mint-tab-item.is-selected {
          border-bottom: 3px solid #FF5A23;
          .mint-tab-item-label {
            color: #FF5A23;
          }
        }
      }
  }
</style>

<style scoped>
    .private-hold-main {
      .icon-back {
        display: inline-block;
        width: 25px;
        height: 25px;
        background-image: url('../../images/icon/backWhite@2x.png');
        background-size: contain;
        background-repeat: no-repeat;
        margin: 5px 0 0 -6px;
      }
      .asset-overview {
        position: relative;
        background: #FD5729;
        padding: 0 17px;
        height: 133px;
        padding-top: 17px;
        color: #fff;
        .item-key {
          display: block;
          font-size: 13px;
          color: #FFF;
          opacity: 0.5;
          margin: 0 0 4px 0;
          height: 18px;
        }
        .item-value {
          display: block;
          font-size: 19px;
          color: #FFF;
          height: 22px;
        }
        .total {
          padding: 5px 0 14px 0;
          .item-value {
            height: 31px;
            font-size: 26px;
          }
        }
        .income {
          display: flex;
          p {
            flex: 1;
          }
        }
        .level-bg {
          position: absolute;
          z-index: 1;
          top: 36px;
          right: 0;
          opacity: 0.3;
          background: #FFF;
          width: 78px;
          height: 29px;
          border-radius: 100px 0 0 100px;
        }
        .level {
          position: absolute;
          z-index: 2;
          top: 36px;
          right: 0;
          width: 78px;
          height: 29px;
          line-height: 26px;
          text-align: center;
          span {
            font-size: 12px;
            &::before {
              content: '';
              display: inline-block;
              margin: 0 4px -2px 0;
              width: 14px;
              height: 13px;
              background: url('../../images/icon/star@2x.png') no-repeat;
              background-size: 100% 100%;
            }
          }
        }
      }
      .asset-detail {
        .tab-nav {
        }
        .noData {
          width: 100%;
          height: 200px;
          text-align: center;
          font-size: 14px;
          color: #666;
          padding-top: 57px;
          background: url('../../images/noPro@2x.png') no-repeat;
          background-size: 170px;
          background-position: center center;
          p {
            margin-top: 160px;
          }
        }
        .company{
          height: 45px;
          line-height: 45px;
          background: #f6f6f6;
          padding:0 15px;
          color:#666;
          font-size:14px;
        }
      }
    }
</style>



