<template>
  <div class="town-list-main">
    <Header
      :fixed="fixed" 
      :fullView="fullView" 
      :right="right"
      @onLeft="goBack"
      :customClass="customClass"
    >
    <span class="bold">特色小镇</span>
    </Header>
    <div class="town-box">
      <mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" @top-status-change="handleTopChange" ref="loadmore">
        <div :style="windowH">
          <ul v-if="townList.length">
            <li v-for="item in townList" @click="goDetail(item.linkUrl)">
              <img :src="item.coverUrl" alt="">
              <h2>{{item.adName}}</h2>
            </li>
          </ul>
          <div v-if="!townList.length && isLoaded" class="no-town">
            <span>更多小镇优选项目，敬请期待</span>
          </div>
        </div>
        <div slot="top">
          <TopStatus :status="topStatus"></TopStatus>
        </div>
      </mt-loadmore>
    </div>
  </div>
</template>
<script>
  import Header from 'components/Header'
  import ua from 'utils/ua'
  import TopStatus from 'components/TopStatus'
  export default {
    name: 'Town',
    data() {
      return {
        fixed: true,
        fullView: true,
        right: {
          show: false
        },
        back: {
          show: true,
          text: '',
          icon: 'icon-back',
        },
        customClass: 'self-header',
        townList: [],
        pageNo: 1,
        allLoaded: false,
        topStatus: '',
        windowH: {},
        isLoaded: false
      }
    },
    components: { 
      TopStatus,
      Header
    },
    mounted() {
      // let title = this.$route.query.title
      // if (title && title !== 'undefined') {
      //   this.setTitle(decodeURIComponent(title))
      // }
      this.fetchTownList({
        pageNo: this.pageNo,
        oldList: []
      })
      let h = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight
      this.windowH = { 'min-height': h + 'px'}
    },
    computed: {
    },
    methods: {
      goBack() {
        this.$router.back()
      },
      fetchTownList(opt) {
        this.isLoaded = false
        this.townList = opt.oldList.concat([{
          linkUrl: '',
          coverUrl: 'https://5b0988e595225.cdn.sohucs.com/images/20170705/36914f2d96c34a86be2a9965a9f8e7cb.png',
          adName: '西湖龙坞茶镇'
        }, {
          linkUrl: '',
          coverUrl: 'https://5b0988e595225.cdn.sohucs.com/images/20170705/2233f8c01d0941dc876a898e25535d9f.png',
          adName: '运河财富小镇'
        }, {
          linkUrl: '',
          coverUrl: 'https://5b0988e595225.cdn.sohucs.com/images/20170705/76483facc5b34c94b2c41a8e93bb538c.png',
          adName: '临安云制造小镇'
        }, {
          linkUrl: '',
          coverUrl: 'https://5b0988e595225.cdn.sohucs.com/images/20170705/be3eca93cfa14a75ac4708e6a1376570.png',
          adName: '余杭梦栖小镇'
        }, {
          linkUrl: '',
          coverUrl: 'https://5b0988e595225.cdn.sohucs.com/images/20170705/c14d8e8be05a4d8abd5306cfe08711e0.png',
          adName: '滨江物联网小镇'
        }])
        opt.callback && opt.callback()
        // api.getTownList({
        //   locationType: 8,
        //   pageNo: opt.pageNo,
        //   pageSize: 4
        // }).then(res => {
        //   if (res.code === 0 && res.data) {
        //     this.townList = opt.oldList.concat(res.data.data || [])
        //     opt.callback && opt.callback()
        //     if (res.data.totalPage <= res.data.pageNo) {
        //       this.allLoaded = true
        //     }
        //   }
        //   this.isLoaded = true
        // }).catch(() => {
        //   this.isLoaded = true
        //   opt.callback && opt.callback()
        // })
      },
      loadTop() {
        this.pageNo = 1
        this.fetchTownList({
          pageNo: this.pageNo,
          oldList: [],
          callback: () => {
            this.$refs.loadmore.onTopLoaded()
          }
        })
        this.allLoaded = false
      },
      loadBottom() {
        this.pageNo++
        this.fetchTownList({
          pageNo: this.pageNo,
          oldList: this.townList,
          callback: () => {
            this.allLoaded = false
            this.$refs.loadmore.onBottomLoaded()
          }
        })
        this.allLoaded = true
      },
      handleTopChange(status) {
        // this.moveTranslate = 1
        this.topStatus = status
      },
      goDetail(url) {
        if (!url) return
        location.href = url
      }
    }
  }
</script>

<style>
.town-list-main {
  .header-wrapper .header {
    &.self-header {
      font-size: 17px;
      i.left{
        line-height: 45px;
        font-size: 28px;
      }
    }
  }
}
</style>
<style scoped>
  .town-list-main {
    .town-box {
      ul {
        margin: 0 20px;
        margin-top: 15px;
        li {
          background: #fff;
          border-radius: 5px;
          margin-bottom: 15px;
          img {
            width: 100%;
            height: 135px;
            border-radius: 5px 5px 0 0;
          }
          h2 {
            padding: 14px 15px;
            font-size: 16px;
            color: #333;
            height: 22px;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
          }
        }
      }
      .no-town {
        margin-top: 150px;
        text-align: center;
        span {
          color: #999;
          font-size: 14px;
          &::before {
            content: '';
            display: block;
            width: 87px;
            height: 87px;
            margin: 0 auto;
            margin-bottom: 10px;
            background:url("./images/no-data.png") center 0 no-repeat;
            background-size: 100% 100%;
          }
        }
      }
    }
  }
</style>


