import { Toast } from 'mint-ui'

let toastList = []
let startTime = false

export default (option) => {
  let type = typeof option
  let opt = Object.assign({}, type === 'object' ? option : { message: option })
  opt._duration = option.duration || 3000
  opt.duration = -1
  toastList.push(opt)
  if (!startTime) {
    run()
  }

  function run() {
    if (!toastList.length) {
      return
    }
    let opt = toastList.shift()
    let toast = Toast(opt)
    if (!startTime) {
      startTime = Date.now()
    }
    setTimeout(() => {
      toast.close()
      startTime = false
      setTimeout(() => {
        run()
      }, 400)
    }, opt._duration)
  }
}



