<template>
  <div class="score-main">
     <Header
      :fixed="fixed"
      :fullView="fullView"
      :right="right"
      @onLeft="onBack"
      :customClass="customClass"
    >悦龙会</Header>
    <div class="card-bg">
      <div class="card" v-show="true">
        <div>
          <div class="card-info-t">
            <p class="card-title">尊敬的{{accountInfo.custName}}，您好!</p>
            <p class="card-score"><span class="score">{{accountInfo.point}}</span>积分</p>
          </div>
          <div class="card-list">
            <div class="card-item">
              <p class="item-t">{{accountInfo.rank}}</p>
              <p class="p-type"><i class="level"></i>会员等级</p>
            </div>
            <div class="card-item">
              <p class="item-t">{{(accountInfo.firstBroker || {}).brokerName}}</p>
              <p class="p-type"><i class="adviser"></i>咨询顾问</p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <mt-loadmore :top-method="loadTop" ref="loadmore"  @top-status-change="handleTopChange">
      <div :style="windowH">
        <div class="score-columns-mian">
          <div class="score-columns-item columns-search" @click="routerLink({path:'',newWebView:true, auth: true, ylhAuth: true})">
          积分明细
          </div>
          <div class="score-columns-item columns-question" @click="routerLink({path:'/faq',newWebView:true,auth: true, ylhAuth: true})">
          常见问题
          </div>
          <div class="score-columns-item columns-vipnotes" @click="routerLink({path: '',newWebView:true,auth: true, ylhAuth: true})">
          会员须知
          </div>
        </div>
        <div class="empty"></div>
        <div class="score-mall">
          <!-- <GiftCell></GiftCell> -->
          <h2 class="title-h2"><span>积分商城</span><a class="more" @click="routerLink({path:'/score/mall',newWebView:true, auth: true, ylhAuth: true})" href="javascript:;">更多</a></h2>
          <ul class="gift-group clearfix" v-if="giftList.length">
            <li is="gift-cell" v-for="(item, index) in giftList" :gift="item" :key="index"></li>
          </ul>
        </div>
        <div class="banner" v-show="bannerList.length>0">
          <h2 class="title-h2"><span>积分活动</span></h2>
          <div class="banner-main">
              <swiper :options="bannerOption">
                <swiper-slide v-for="(item, index) in bannerList" :key="index">
                  <div class="banner-top" href="javascript:;" @click="toOutLink(item.linkUrl)">
                    <div class="img-radius"><img :src="item.imageUrl" alt=""></div>
                    <p>{{item.adName}}</p>
                  </div>
                </swiper-slide>
                <div v-show="bannerList.length > 1" class="swiper-pagination" slot="pagination"></div>
              </swiper>
          </div>
        </div>
      </div>
      <div slot="top">
        <TopStatus :status="topStatus"></TopStatus>
      </div>
    </mt-loadmore>
  </div>
</template>
<script>
  import Header from 'components/Header'
  import GiftCell from './GiftCell'
  import ua from 'utils/ua'
  import 'swiper/dist/css/swiper.css'
  import {swiper, swiperSlide} from 'vue-awesome-swiper'
  import TopStatus from 'components/TopStatus'
  export default {
    name: 'Score',
    data() {
      return {
        title: '悦龙会',
        fixed: true,
        fullView: true,
        right: {
          show: false
        },
        customClass: 'self-header',
        topStatus: '',
        giftList: [],
        bannerList: [],
        accountInfo: {},
        bannerOption: {
          spaceBetween: 30,
          centeredSlides: true,
          autoplay: {
            delay: 2500,
            disableOnInteraction: false,
          },
          pagination: {
            el: '.swiper-pagination',
            clickable: true,
          },
        },
        windowH: {}
      }
    },
    components: {
      Header,
      "gift-cell": GiftCell,
      swiper,
      swiperSlide,
      TopStatus
    },
    mounted(){
      let h = (window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight) - 200
      this.windowH = { 'min-height': h + 'px'}
      this.fetchInfo()
    },
    computed: {
    },
    methods: {
      handleTopChange(status) {
        this.topStatus = status
      },
      fetchInfo(callback){
        this.giftList = [{
          imgUrl: 'https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=1658810097,2551688508&fm=26&gp=0.jpg',
          title: 'H12-iPhone X',
          point: '20000'
        }, {
          imgUrl: 'https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=1813392248,4273069575&fm=26&gp=0.jpg',
          title: 'H10-beats耳机',
          point: '1000'
        }, {
          imgUrl: 'https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=1029060034,2889709665&fm=26&gp=0.jpg',
          title: 'H11-海尔挂烫机',
          point: '3200'
        }]
        this.bannerList = [{
          adName: '运动新型象',
          linkUrl: '',
          imageUrl: 'https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=3748595743,2042953998&fm=26&gp=0.jpg'
        }, {
          adName: '夏装清仓',
          linkUrl: '',
          imageUrl: 'https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=3389012736,1007124726&fm=26&gp=0.jpg'
        }, {
          adName: '水果大促',
          linkUrl: '',
          imageUrl: 'https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=4152093700,520592336&fm=26&gp=0.jpg'
        }]
        this.accountInfo = {
          custName: '杨澜',
          point: '15000',
          rank: '白金级',
          firstBroker: {
            brokerName: '张三'
          }
        }
        callback && callback()
        // Promise.all([
        //   api.getDragonMeeting().then(res => {
        //     if (res.code === 0 && res.data) {
        //       if (res.data.list){
        //         this.giftList = res.data.list.slice(0, 4) || []
        //         this.bannerList = res.data.integralActivity || []
        //       }
        //     }
        //     callback && callback()
        //   }).catch(() => {
        //     callback && callback()
        //   }),
        //   api.getAccountInfo().then(res => {
        //     if (res.code === 0 && res.data){
        //       this.accountInfo = res.data
        //     }
        //     callback && callback()
        //   }).catch(() => {
        //     callback && callback()
        //   })
        // ])
      },
      loadTop(){
        this.fetchInfo(() => {
          this.$refs.loadmore && this.$refs.loadmore.onTopLoaded && this.$refs.loadmore.onTopLoaded()
        })
      },
      routerLink(opt) {
        this.$router.push(opt.path)
      },
      toOutLink(path){
        // location.href = path
      },
      onBack(){
        this.$router.back()
      }
    }
  }
</script>
<style>
  .score-main {
    .header-wrapper .header{
      &.self-header{
        background:url("./images/home-bg.png") 0 0 no-repeat;
        background-size:375px;
        border:none;
        color:#fff;
        i.left{
          line-height: 45px;
          color:#fff;
          font-size:28px;
          margin-top:10px;
        }
      }
    }
    .swiper-pagination{
      bottom:-17px;
      .swiper-pagination-bullet{
        opacity: 0.8;
        background: #ddd;
        width:4px;
        height: 4px;
        border-radius:5px;
      }
      .swiper-pagination-bullet-active{
        width:15px;
      }
    }
  }
</style>

<style scoped>
  .score-main {
    /* 此处定义当前页面样式 */
    background: #fff;
    .empty{
      height:10px;
      background: #F6F6F6;
    }
    .mint-loadmore{
      margin-top:180px;
    }
    .title-h2{
      height:28px;
      line-height: 28px;
      margin-top:18px;
      margin-bottom:8px;
      font-size:20px;
      color:#333;
      span{
        font-weight: 500;
      }
      a{
        color:#AAA;
        font-size:14px;
        float:right;
      }
    }
    .card-bg{
      position: fixed;
      width: 100%;
      padding:0 20px; 
      box-sizing: border-box;
      z-index: 999;
      height:180px;
      background:url("./images/home-bg.png") 0px -64px no-repeat ;
      background-size:375px;
    }
    .card{
      height:180px;
      background: #fff;
      box-shadow: 0px 2px 10px  rgba(0,0,0,0.05);
      border-radius:10px;
      padding:10px;
      color:#666;
      font-size:12px;
      box-sizing:border-box;
      p{
        text-align: center;
      }
      .p-type{
        color:#aaa;
      }
      .card-info-t{
        height:100px;
        .card-title{
          line-height: 28px;
          margin:8px 0;
          margin-bottom:10px;
          font-size:20px;
          font-weight:500;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
        .card-score{
          height: 50px;
          color:#aaa;
          .score{
            font-size:36px;
            color:#333;
            margin-right:5px;
            font-weight: 450;
          }
        }
      }

      .card-list{
        display:flex;
        height:61px;
        border-top:1Px solid #f6f6f6;
        padding-top:17px;
        box-sizing:border-box;
        .card-item{
          width: 50%;
          flex: 1;
          height:32px;
          &:not(:last-child){
            border-right:1Px solid #f6f6f6;
          }
          .item-t{
            color:#DBA44F;
            font-size:14px;
            margin-top:-5px;
            font-weight:500;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
          }
          i{
            display: inline-block;
            vertical-align: middle;
            margin-top:-1px;
            width:12px;
            height:16px;
            margin-right:1px;
            &.adviser{
              background:url("./images/adviser.png") no-repeat;
              background-size:12px 16px;
            }
            &.level{
              background:url("./images/level.png") no-repeat;
              background-size:12px 16px;
            }
            &.danger{
              background:url("./images/danger.png") no-repeat;
              background-size:12px 16px;
            }
          }

        }
      }
    }
    .score-columns-mian{
      height: 81px;
      display:flex;
      margin-top:15px;
      color:#666;
      background: #fff;
      padding:0 22px;
      .score-columns-item{
        height: 94px;
        box-sizing:border-box;
        font-size: 12px;
        flex: 1;
        text-align: center;
        padding-top:44px;
        &.columns-search{
          background:url("./images/search.png") center 4px no-repeat;
          background-size:36px;
        }
        &.columns-question{
          background:url("./images/question.png") center 4px no-repeat;
          background-size:36px;
        }
        &.columns-vipnotes{
          background:url("./images/vipnotes.png") center 4px no-repeat;
          background-size:36px;
        }
      }
    }
    .score-mall,.banner{
      margin-top:10px;
      background:#fff;
    }
    .score-mall {
      padding: 0 20px 0px 20px;
    }
    .banner {
      padding: 0 0 17px 0;
      margin: 0 20px;
      border-top: 1Px solid #f2f2f2;
    }
    .swiper-container{
      overflow:inherit;
    }
    .banner-main{
      height:200px;
      margin-bottom:50px;
      border-radius:5px;

      box-sizing: border-box;
      .banner-top{
        height:180px;
        border-radius:5px 5px 0 0;
        overflow: hidden;
      }
      p{
        line-height:47px;
        padding:0 15px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        font-size:15px;
        color:#333;
        border: 1Px solid #eee;
        border-top: 0;
        border-radius: 0 0 5px 5px;
      }
      .img-radius{
        height:130px;
        border-radius:5px 5px 0 0;
        overflow: hidden;
      }
      img{
        width:100%;
        height:130px;

      }
    }
  }
</style>



