<template>
  <div class="user-login-main">
    <self-header 
      :fixed="fixed" 
      :fullView="fullView" 
      :right="right"
      @onLeft="goBack"
      :customClass="customClass"
      ></self-header>
    <div class="popLogin-wrapper">
      <div class="login-box">
        <div class="content">
          <div class="logo"></div>
          <p class="text">尊贵的悦龙会会员<br/>为了保证安全，请输入您的悦龙会登录密码</p>
          <p class="login-type">
            <span class="label">证件号登录</span>
            <mt-switch v-model="isIdentity" @change="changeSwitch"></mt-switch>
          </p>
          <ul class="login-input">
            <li v-show="isIdentity">
              <mt-field label="" placeholder="请输入证件号码" v-model="identityNo" :disableClear="true"></mt-field>
            </li>
            <li v-show="!isIdentity" class="li-mobileNo">
              <mt-field label="" type="number" placeholder="请输入手机号" v-model="mobileNo" :disableClear="true" :disabled="hasMobile"></mt-field>
            </li>
            <li>
              <mt-field label="" :type="pwdType" placeholder="请输入密码" v-model="password" :disableClear="true" @change="pwdChange">
                <div class="eye-box"><span class="icon-eye off" :class="{off: pwdType==='password', on: pwdType==='text' }" @click="changePwdType"></span></div>
              </mt-field>
            </li>
          </ul>
          <p class="msg" :class="{show: showMsg===true}">{{msg}}</p>
          <p class="btnlogin">
            <mt-button size="large" type="primary" :disabled="isDisabled" @click="submitLogin">登&nbsp;&nbsp;&nbsp;录
            </mt-button>
          </p>
          <div class="bottom">
            <p class="checkbox-box">
              <input type="checkbox" id="rememberPwd" class="checkbox-input" v-model="isRememberPwd" @change="changeRemember"/>
              <label for="rememberPwd" class="checkbox-label">记住密码</label>
            </p>
            <p>
              <a class="reg" href="javascript:;" @click="goReg" v-show="!isIdentity">注册</a>
              <a class="forget" href="javascript:;" @click="!isIdentity ? routerLink('/user/findpwd/step1', true) : callTel()">忘记密码</a>
              <span class="icon-tel" @click="callTel"></span>
            </p>
          </div>
        </div>
      </div>
      <loading cssText="background: #FFF; box-shadow: 0 0 0 1000px rgba(0,0,0,0.1); z-index: 3;" :show="showLoading" ></loading>
    </div>
  </div>
</template>
<script>
  import selfHeader from 'components/Header'
  import ua from 'utils/ua'
  import loading from 'components/Loading'
  export default {
    name: 'Login',
    components: { loading, selfHeader },
    data() {
      return {
        fixed: true,
        fullView: true,
        right: {
          show: false
        },
        back: {
          show: true,
          text: '',
          icon: 'icon-back',
        },
        customClass: 'self-header',
        isIdentity: false,  // 是否证件号登录
        mobileNo: '',
        password: '',
        identityNo: '',
        pwdType: 'password',
        isRememberPwd: false,
        msg: '',
        showMsg: false,
        inputFlag: false,
        showLoading: false,
        hasMobile: true,
        test: true
      }
    },
    mounted() {
      this.initLogin()
    },
    watch: {
      privateLoginShow() {
        this.initLogin()
      },
    },
    computed: {
      // ...mapGetters(['loginSuccessCallback', 'loginErrorCallback']),
      privateLoginShow: {
        get() {
          // return this.$store.getters.privateLoginShow
        },
        set() {}
      },
      isDisabled: {
        get: function () {
          if (!this.isIdentity) {
            return !this.password
          } else {
            return !this.identityNo || !this.password
          }
        },
        set: function () {
        }
      }
    },
    methods: {
      // ...mapActions(['hidePrivateLogin']),
      initLogin() {
        // bridge.getUserInfo({
        //   success: (userInfo) => {
        //     setUserInfo(userInfo)
        //     const user = getUserInfo() || {}
        //     this.mobileNo = user.tel
        //     let tel = this.mobileNo
        //     if (!tel) {
        //       this.hasMobile = false
        //     }
        //     let rememberUserInfo = JSON.parse(localStorage.getItem('rememberUserInfo') || '{}')
        //     this.password = rememberUserInfo[tel] && rememberUserInfo[tel].pwd
        //     this.identityNo = localStorage.getItem('identityNo') || ''
        //     if (this.password) {
        //       this.isRememberPwd = true
        //     }
        //   }
        // })
      },
      submitLogin() {
         this.$router.push({ path: '/user/account' })
        // this.isDisabled = true
        // this.showLoading = true
        // let params = {}
        // let pwd = this.inputFlag ? md5(this.password) : this.password
        // if (!this.isIdentity) {
        //   params = {
        //     mobileNo: this.mobileNo,
        //     password: pwd
        //   }
        // } else {
        //   params = {
        //     identityNo: this.identityNo,
        //     password: pwd
        //   }
        // }
        // api.privatelogin(params).then(res => {
        //   this.isDisabled = false
        //   this.showLoading = false
        //   if (res.code === 0) {
        //     this.hidePrivateLogin()
        //     ua.jcy && bridge.closeWebView()
        //     setYlhLoginTime()
        //     this.loginSuccessCallback(res.data)
        //     this.rememberPwd()
        //     this.resetFieldData()
        //   } else {
        //     this.msg = res.errmsg
        //     this.showMsg = true
        //     this.loginErrorCallback(res)
        //   }
        // }).catch(() => {
        //   this.isDisabled = false
        //   this.showLoading = false
        //   this.loginErrorCallback()
        // })
      },
      closeLogin() {
        this.hidePrivateLogin()
        this.resetFieldData()
      },
      rememberPwd() {
        // let rememberUserInfo = JSON.parse(localStorage.getItem('rememberUserInfo') || '{}')
        // let pwd = this.inputFlag ? md5(this.password) : this.password
        // if (!this.isIdentity) {
        //   let tel = this.mobileNo
        //   rememberUserInfo[tel] = rememberUserInfo[tel] || {}
        //   rememberUserInfo[tel].pwd = this.isRememberPwd ? pwd : ''
        // } else {
        //   let identityNo = this.identityNo
        //   rememberUserInfo[identityNo] = rememberUserInfo[identityNo] || {}
        //   rememberUserInfo[identityNo].pwd = this.isRememberPwd ? pwd : ''
        //   localStorage.setItem('identityNo', identityNo)
        // }
        // localStorage.setItem('rememberUserInfo', JSON.stringify(rememberUserInfo))
      },
      resetFieldData() {
        // 初始化表单数据
        this.identityNo = ''
        this.pwdType = 'password'
        this.msg = ''
        this.showMsg = false
        this.isIdentity = false
        this.inputFlag = false
        this.isRememberPwd = false
      },
      callTel() {
        // bridge.callTel({
        //   tel: '4006881888'
        // })
      },
      changeSwitch(e) {
        this.msg = ''
        this.pwdType = 'password'
        console.log(e, 'e')
        this.isIdentity = !e
        let rememberUserInfo = JSON.parse(localStorage.getItem('rememberUserInfo') || '{}')
        let tel = this.mobileNo
        let identityNo = this.identityNo
        if (e) {
          this.password = rememberUserInfo[tel] && rememberUserInfo[tel].pwd
          if (this.password) {
            this.isRememberPwd = true
          } else {
            this.isRememberPwd = false
          }
        } else {
          if (identityNo) {
            this.isRememberPwd = true
          } else {
            this.isRememberPwd = false
          }
          this.password = rememberUserInfo[identityNo] && rememberUserInfo[identityNo].pwd
        }
      },
      changeRemember() {
        console.log(this.isRememberPwd)
        if (!this.isRememberPwd) {
          this.password = ''
        }
      },
      changePwdType() {
        if (this.pwdType === 'password') {
          this.pwdType = 'text'
        } else {
          this.pwdType = 'password'
        }
        if (!this.inputFlag) {
          this.password = ''
        }
        if (this.isRememberPwd && this.pwdType === 'text') {
          this.password = ''
        }
      },
      pwdChange() {
        if (this.password !== localStorage.getItem('pwd')) {
          this.inputFlag = true
        }
      },
      routerLink(path, keepPopup) {
        this.$router.push({ path: path })
      },
      goReg() {
        // this.routerLink(`/about/protocol/register?mobileNo=${this.mobileNo}`, true)
        // let path = '/about/protocol/register'
        // this.$router.push({ path, query: { mobileNo: this.mobileNo } })
      },
      goBack() {
        this.$router.back()
      },
    }
  }
</script>
<style>
body {
  background: #fff;
}
.user-login-main {
  /* 此处定义当前页面样式 */
  .header-wrapper .header {
    background: transparent;
    &.self-header {
      border: none;
      font-size: 17px;
      i.left{
        line-height: 45px;
        font-size: 28px;
      }
    }
  }
  .login-input {
    .mint-cell-wrapper {
      padding: 0;
      background: #fff;
    }
    .mint-cell-value {
      input {
        border: 1Px solid #f2f2f2;
      }
      input::-webkit-input-placeholder{
        color: #D9D9D9;
        font-size: 15px;
      }
    }
    .mint-field-core {
      color: #333;
      font-size: 15px;
      border: 1Px solid #D9D9D9;
      border-radius: 4px;
      padding: 10px 12px;
      &:disabled {
        background: #F6F6F6;
        color: #ccc;
      }
    }
  }
  .login-type {
    .mint-switch-core {
      width: 38px;
      height: 20px;
      &::before {
        width: 36px;
        height: 18px;
      }
      &::after {
        width: 18px;
        height: 18px;
      }
    }
    .mint-switch-input:checked + .mint-switch-core {
      border-color: #FE5B1C;
      background-color: #FE5B1C;
    }
  }
  .popLogin-wrapper {
    .v-modal {
      display: none;
    }
    .mint-popup-bottom {
      width: 100%;
      border-radius: 10px 10px 0 0;
    }
    .mint-indicator-wrapper {
      z-index: 9999;
    }
    .li-mobileNo {
      .mint-cell-value {
        input:disabled {
          opacity: 1;
          color: #ccc;
        }
      }
    }
  }
}
</style>

<style scoped>
    .user-login-main {
      /* 此处定义当前页面样式 */
      .header-wrapper .header {
        background: transparent;
        &.self-header {
          border: none;
          font-size: 17px;
          i.left{
            line-height: 45px;
            font-size: 28px;
          }
        }
      }
      .mint-toast {
        z-index: 3000;
      }
      .login-box {
        .top {
          height: 50px;
          line-height: 50px;
          display: flex;
          border-bottom: 1Px solid #F2F2F2;
          h2 {
            flex: 1;
            font-size: 16px;
            color: #333;
            text-align: center;
          }
          .icon-close {
            margin: 18px 0 0 16px;
            display: inline-block;
            width: 16px;
            height: 16px;
            background: url('./images/close@2x.png') no-repeat;
            background-size: 100% 100%;
          }
          .icon-tel {
            margin: 16px 16px 0 0;
            display: inline-block;
            width: 17px;
            height: 18px;
            background: url('./images/phone@2x.png') no-repeat;
            background-size: 100% 100%;
          }
        }
        .content {
          padding: 15px 20px;
          .logo {
            margin: 0 auto;
            width: 57px;
            height: 57px;
            background: url('./images/logo-ylh@3x.png') no-repeat;
            background-size: 100% 100%;
          }
          .text {
            margin: 20px 0 25px;
            text-align: center;
            font-size: 14px;
            color: #666;
            line-height: 1.8;
          }
          .msg {
            visibility: hidden;
            color: red;
            line-height: 23px;
            height: 23px;
            font-size: 12px;
            &.show {
              visibility: inherit;
            }
          }
          .login-type {
            display: flex;
            .label {
              font-size: 14px;
              color: #666;
              margin-right: 13px;
            }
          }
          ul {
            margin-top: 12px;
            li {
              &:last-child {
                margin-top: 5px;
              }
            }
            .eye-box {
              position: absolute;
              top: -20px;
              right: 2px;
              z-index: 2;
              width: 25px;
              padding: 0 15px 0 22.5px;
              height: 40px;
              border-radius: 4px;
              background: #fff;
              .icon-eye {
                display: inline-block;
                width: 25px;
                height: 18px;
                margin-top: 12px;
                &.off {
                  background: url('./images/eyeOff@2x.png') no-repeat;
                  background-size: 100% 100%;
                }
                &.on {
                  background: url('./images/eyeOn@2x.png') no-repeat;
                  background-size: 100% 100%;
                }
              }
            }
          }
          .btnlogin {
            .mint-button {
              height: 44px;
              font-size: 17px;
              border-radius: 5px;
            }
            .mint-button--primary {
              background: #FF541B;
            }
            .mint-button.is-disabled {
              opacity: 1;
              background: #D9D9D9;
            }
          }
          .bottom {
            display: flex;
            margin-top: 15px;
            p {
              flex: 1;
              text-align: left;
              font-size: 14px;
              color: #666;
              &:last-child {
                text-align: right;
              }
            }
            .checkbox-box {
              position: relative;
              box-sizing: border-box;
              .checkbox-input {
                display: none;
              }
              .checkbox-label {
                padding-left: 25px;
                &::before {
                  content: '';
                  position: absolute;
                  top: 1px;
                  left: 0px;
                  display: inline-block;
                  height: 16px;
                  width: 16px;
                  border-radius: 3px;
                  border: 1Px solid #ccc;
                  background-color: #fff;
                }
              }
              .checkbox-input:checked + .checkbox-label:before {
                border: 1Px solid #FF541B;
                background: #FF541B;
              }
              .checkbox-input:checked + .checkbox-label:after {
                border: 2Px solid #fff;
                border-left: 0;
                border-top: 0;
                border-radius: 0;
                content: '';
                top: 3px;
                left: 6px;
                position: absolute;
                width: 4px;
                height: 8px;
                background: #FF541B;
                transform: rotate(45deg) scale(1);
              }
            }
            a {
              color: #666;
              padding: 0 10px;
              border-right: 1Px solid #F2F2F2;
              &.reg {
                color: #FD5729;
              }
              &.forget {
              }
            }
            span.icon-tel {
              margin: 0 10px -2px 5px;
              display: inline-block;
              width: 17px;
              height: 18px;
              background: url('./images/phone@2x.png') no-repeat;
              background-size: 100% 100%;
            }
          }
        }
      }
    }
</style>


