<template>
  <div class="score-mall-main">
    <self-header
      :fixed="fixed"
      :fullView="fullView"
      :right="tradeRecord"
      @onLeft="goBack"
      @onRight="goTradeRecord"
      :customClass="customClass">积分商城
    </self-header>
    <mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" @top-status-change="handleTopChange" ref="loadmore">
      <div class="header-main">
        <div class="score" @click="goTradeRecord">
          <p class="score-num">{{memberInfo.point}}</p>
          <p>当前积分<span class="icon-scoredetail"></span></p>
        </div>
        <div class="line"></div>
        <div class="level">
          <i class="icon-level" :class="levelMap[memberInfo.levelName]"></i>
          <p>会员等级 {{memberInfo.levelName}}</p>
        </div>
      </div>
      <!-- <div class="banner-wrapper">
        <img src="./images/bannerToMoney.png" />
      </div> -->
      <div class="gift-wrapper" :style="windowH">
        <h2 class="h2Title">全部礼品 <a href="javascript:;" class="search-btn" @click="routerLink({path:'/score/searchGift',newWebView: true, auth: true, ylhAuth: true})"></a></h2>
        <div class="giftAll">
          <div class="tab-nav-wrapper">
            <div class="tab-convertible" @click="changeTabConvertible">
               <span :class="{selected: isConvertible}">可兑换</span>
            </div>
            <div class="tab-nav">
              <div class="tab-nav-group">
                <span :class="{selected: active==='gift-all' && isConvertible===false}" @click="changeTab('gift-all')">全部</span>
                <span :class="{selected: active==='gift-1010' && isConvertible===false}" @click="changeTab('gift-1010')">100以内</span>
                <span :class="{selected: active==='gift-1030' && isConvertible===false}" @click="changeTab('gift-1030')">101-300积分</span>
                <span :class="{selected: active==='gift-1050' && isConvertible===false}" @click="changeTab('gift-1050')">301-500积分</span>
                <span :class="{selected: active==='gift-1100' && isConvertible===false}" @click="changeTab('gift-1100')">501-1000积分</span>
                <span :class="{selected: active==='gift-1200' && isConvertible===false}" @click="changeTab('gift-1200')">1000积分以上</span>
              </div>
            </div>
          </div>
          <div class="tab-container">
            <div class="convertible-content" v-show="isConvertible">
              <ul class="gift-group clearfix" v-if="allGifts.length">
                <li is="gift-cell" v-for="(item, index) in allGifts" :key="index" :gift="item" @onDetail="gotoDetail(item.code)"></li>
              </ul>
              <p class="noData" v-if="!allGifts.length && isLoaded">暂无合适的礼品</p>
            </div>
            <mt-tab-container v-model="active" v-show="!isConvertible">
              <mt-tab-container-item id="gift-all">
                <ul class="gift-group clearfix" v-if="allGifts.length">
                  <li is="gift-cell" v-for="(item, index) in allGifts" :key="index" :gift="item" @onDetail="gotoDetail(item.code)"></li>
                </ul>
                <p class="noData" v-if="!allGifts.length && isLoaded">一大波礼品在准备中，敬请期待</p>
              </mt-tab-container-item>
              <mt-tab-container-item id="gift-1010">
                <ul class="gift-group clearfix" v-if="allGifts.length">
                  <li is="gift-cell" v-for="(item, index) in allGifts" :key="index" :gift="item" @onDetail="gotoDetail(item.code)"></li>
                </ul>
                <p class="noData" v-if="!allGifts.length && isLoaded">暂无合适的礼品</p>
              </mt-tab-container-item>
              <mt-tab-container-item id="gift-1030">
                <ul class="gift-group clearfix" v-if="allGifts.length">
                  <li is="gift-cell" v-for="(item, index) in allGifts" :key="index" :gift="item" @onDetail="gotoDetail(item.code)"></li>
                </ul>
                <p class="noData" v-if="!allGifts.length && isLoaded">暂无合适的礼品</p>
              </mt-tab-container-item>
              <mt-tab-container-item id="gift-1050">
                <ul class="gift-group clearfix" v-if="allGifts.length">
                  <li is="gift-cell" v-for="(item, index) in allGifts" :key="index" :gift="item" @onDetail="gotoDetail(item.code)"></li>
                </ul>
                <p class="noData" v-if="!allGifts.length && isLoaded">暂无合适的礼品</p>
              </mt-tab-container-item>
              <mt-tab-container-item id="gift-1100">
                <ul class="gift-group clearfix" v-if="allGifts.length">
                  <li is="gift-cell" v-for="(item, index) in allGifts" :key="index" :gift="item" @onDetail="gotoDetail(item.code)"></li>
                </ul>
                <p class="noData" v-if="!allGifts.length && isLoaded">暂无合适的礼品</p>
              </mt-tab-container-item>
              <mt-tab-container-item id="gift-1200">
                  <ul class="gift-group clearfix" v-if="allGifts.length">
                    <li is="gift-cell" v-for="(item, index) in allGifts" :key="index" :gift="item" @onDetail="gotoDetail(item.code)"></li>
                  </ul>
                  <p class="noData" v-if="!allGifts.length && isLoaded">暂无合适的礼品</p>
              </mt-tab-container-item>
            </mt-tab-container>
          </div>
        </div>
      </div>
      <div slot="top">
        <TopStatus :status="topStatus"></TopStatus>
      </div>
    </mt-loadmore>
  </div>
</template>
<script>
  import GiftCell from '../GiftCell'
  import ua from 'utils/ua'
  import selfHeader from 'components/Header'
  import TopStatus from 'components/TopStatus'
  export default {
    name: 'Mall',
    components: {
      GiftCell,
      selfHeader,
      TopStatus
    },
    data() {
      return {
        fixed: true,
        jcy: ua.jcy,
        levelMap: {
          '白银级': 'silver',
          '黄金级': 'gold',
          '白金级': 'platina',
          '钻石级': 'diamond',
          '超钻级': 'superDiamond',
        },
        fullView: true,
        tradeRecord: {
          show: true,
          text: '',
          icon: 'icon-money',
        },
        customClass: 'self-header',
        memberInfo: {},
        allGifts: [],
        isConvertible: true,
        active: '',
        pageNo: 1,
        allLoaded: false,
        bottomAllLoaded: false,
        giftType: 1999,
        userInfo: {},
        topStatus: '',
        windowH: {},
        isLoaded: false
      }
    },
    mounted() {
      this.fetchMemberInfo()
      this.fetchGifts({
        pageNo: 1,
        type: this.giftType,
        oldList: []
      })
      let h = (window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight) - 200
      this.windowH = { 'min-height': h + 'px'}
    },
    watch: {
      active(val) {
        // console.log(val, 'val');
        let type = ''
        if (!val) {
          type = 1999
        } else {
          let num = val.split('gift-')[1]
          type = num === 'all' ? null : Number(num)
        }
        console.log(type, 'type');
        this.giftType = type
        this.fetchGifts({
          pageNo: 1,
          type: type,
          oldList: []
        })
      }
    },
    methods: {
      fetchMemberInfo() {
        this.memberInfo = {
          levelName: '白金级',
          point: 15000
        }
        // api.getMemberInfo({}).then(res => {
        //   if (res.code === 0) {
        //     this.memberInfo = res.data
        //   } else {
        //     console.log(res.errmsg)
        //   }
        // })
      },
      fetchGifts(opt) {
        this.allGifts = opt.oldList.concat([{
          imgUrl: 'https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=1658810097,2551688508&fm=26&gp=0.jpg',
          title: 'H12-iPhone X',
          point: '20000'
        }, {
          imgUrl: 'https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=1813392248,4273069575&fm=26&gp=0.jpg',
          title: 'H10-beats耳机',
          point: '1000'
        }, {
          imgUrl: 'https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=1029060034,2889709665&fm=26&gp=0.jpg',
          title: 'H11-海尔挂烫机',
          point: '3200'
        }])
        opt.callback && opt.callback()
        // this.isLoaded = false
        // api.getAllGiftList({
        //   pageNo: opt.pageNo,
        //   pageSize: 12,
        //   type: opt.type,
        // }).then(res => {
        //   if (res.code === 0 && res.data) {
        //     this.allGifts = opt.oldList.concat(res.data.data || [])
        //     opt.callback && opt.callback()
        //     if (res.data.totalPage <= res.data.pageNo) {
        //       // 加载完毕
        //       this.allLoaded = true
        //     }
        //   }
        //   this.isLoaded = true
        // }).catch(() => {
        //   opt.callback && opt.callback()
        //   this.isLoaded = true
        // })
      },
      loadTop() {
        this.fetchMemberInfo()
        this.pageNo = 1
        this.fetchGifts({
          pageNo: this.pageNo,
          type: this.giftType,
          oldList: [],
          callback: () => {
            this.$refs.loadmore.onTopLoaded()
          }
        })
        this.allLoaded = false
      },
      loadBottom () {
        this.pageNo++
        this.fetchGifts({
          pageNo: this.pageNo,
          type: this.giftType,
          oldList: this.allGifts,
          callback: () => {
            this.allLoaded = false
            this.$refs.loadmore.onBottomLoaded()
          }
        })
        this.allLoaded = true
      },
      handleTopChange(status) {
        // this.moveTranslate = 1
        this.topStatus = status
      },
      changeTabConvertible() {
        if (this.isConvertible) {
          return
        }
        this.isConvertible = true
        this.pageNo = 1
        this.giftType = 1999
        this.active = ''
      },
      changeTab(param) {
        this.isConvertible = false
        this.active = param
        this.pageNo = 1
      },
      goBack() {
        this.$router.back()
      },
      goTradeRecord() {
        // this.$router.push({path: path})
      },
      routerLink(opt) {
        // this.$router.push(opt.path)
      },
      gotoDetail(id) {
        // let path = `/score/${id}/detail`
        // this.$router.push(path)
      }
    }
  }
</script>
<style>
.score-mall-main {
.header-wrapper .header{
  &.self-header{
    background: url("../images/headerBg@2x.png") 0 0 no-repeat;
    background-size: 375px;
    border: none;
    color: #fff;
    i.left{
      line-height: 45px;
      color:#fff;
      font-size:28px;
      margin-top:10px;
    }
    .icon-money {
      display: inline-block;
      width: 26px;
      height: 22px;
      background-image: url('../images/iconMoney@2x.png');
      background-size: 100% 100%;
      background-repeat: no-repeat;
      margin: 7px 0px 0 0;
    }
  }
}
}
</style>

<style scoped>
    .score-mall-main {
      .header-main {
          background-image: url('../images/mallBg@2x.png');
          background-size: 100% 100%;
          display: flex;
          padding: 34px 0 42px 0;
          text-align: center;
          font-size: 12px;
          color: #fff;
          .score {
            flex: 1;
            .score-num {
              font-size: 29px;
              min-height: 34px;
            }
            .icon-scoredetail {
              display: inline-block;
              width: 13px;
              height: 13px;
              margin: 0 0 -2px 2px;
              background: url('../images/score-detail@2x.png') no-repeat;
              background-size: contain;
            }
          }
          .line {
            width: 1px;
            height: 52px;
            background: #FFF;
            opacity: 0.3;
          }
          .level {
            flex: 1;
            .icon-level {
              display: inline-block;
              width: 40px;
              height: 40px;
              min-height: 36px;
              background-size: contain;
              background-repeat: no-repeat;
              margin: -8px 0 0 0;
              &.silver {
                background-image: url('../images/level-silver@2x.png');
              }
              &.gold {
                background-image: url('../images/level-gold@2x.png');
              }
              &.platina {
                background-image: url('../images/level-platina@2x.png');
              }
              &.diamond {
                background-image: url('../images/level-diamond@2x.png');
              }
              &.superDiamond {
                background-image: url('../images/level-superDiamond@2x.png');
              }
            }
          }
        }
      .banner-wrapper {
        margin: 10px 0;
        img {
          width: 100%
        }
      }
      .gift-wrapper {
        background: #fff;
        margin: 10px 0;
        overflow: hidden;
        clear: both;
        .h2Title {
          font-size: 19px;
          font-weight: bold;
          color: #333;
          padding: 14px 20px 14px 20px;
        }
        .gift-group {
          margin: 0 20px;
          clear: both;
        }
      }
      .giftJX {
        width: 100%;
      }
      .noSwipeH {
        height: 325px;
      }
      .swipeH {
        height: 340px;
      }
      .giftAll {
        width: 100%;
        .tab-nav-wrapper {
          position: relative;
          height: 45px;
          margin: 0 20px 10px;
          border-top: 1Px solid #eee;
          .tab-convertible {
            position: absolute;
            top: 0;
            left: 0;
            width: 70px;
            height: 38px;
            background: #fff;
            box-shadow: 20px 0px 15px #fff;
            span {
              display: block;
              margin: 10px 0 0 0;
              width: 63px;
              height: 26px;
              line-height: 26px;
              font-size: 13px;
              color: #AAA;
              background: #F6F6F6;
              border-radius: 100px;
              text-align: center;
              &.selected {
                color: #FD5729;
                background: #FFEDE8;
              }
            }
          }
          .tab-nav {
            margin: 0 0 0 70px;
            padding: 0 0 0 10px;
            overflow-x: scroll;
            overflow-y: hidden;
            .tab-nav-group {
              width: 580px;
              span {
                display: inline-block;
                padding: 5px 10px;
                height: 38px;
                line-height: 38px;
                font-size: 13px;
                color: #AAA;
                &.selected {
                  padding: 2px 10px;
                  font-size: 15px;
                  color: #FD5729;
                }
              }
            }
          }
        }
      }
      .noData {
        padding: 0 0 20px 0;
        font-size: 12px;
        color: #999;
        text-align: center;
      }
      .search-btn{
        width:18px;
        height:18px;
        float:right;
        background: url("../images/search-btn.png") 0 0 no-repeat;
        background-size: 100%;
        margin-top:5px;
      }
    }
</style>



