<template>
  <div class="sub-header" 
  :class="{'iphoneX':iphoneX,
  'iphone-normal':iphoneNormal,
  'fixed':fixed,
  'animated': true,
  'fadeOutUp':fadeOutUp,
  'fadeInDown':fadeInDown,
  'sole-fixed':sole}">
    <div :class="{'sub-content':true,}">
      <slot name="sub"></slot>
    </div>
  </div>
</template>
<script>
  import ua from 'utils/ua'
  export default {
    name: 'headerSubheader',
    props: {
      fadeOut: {
        type: Boolean,
        'default': false
      },
      fixed: {
        type: Boolean,
        'default': false
      },
      fullView: {
        type: Boolean,
        'default': false
      },
      sole: {
        type: Boolean,
        'default': false
      },
      fadeOutUp: {
        type: Boolean,
        'default': false
      },
      fadeInDown: {
        type: Boolean,
        'default': false
      },
    },
    data (){
      return {
        iphoneNormal: false,
        iphoneX: false,
      }
    },
    created(){
      this.setPosition()
    },
    computed: {},
    mounted(){
      window.onresize = () => {
        this.setPosition()
      }
    },
    methods: {
      setPosition(){
        if (this.fullView) {
          if (ua.ios) {
            if (ua.isIphoneX) {
              this.iphoneX = true;
            } else {
              this.iphoneNormal = true;
            }
          }
        }
      }
    }
  }
</script>
<style lang="postcss" scoped>

.sub-header {
  transition: all 1.5s ease;
  height: 40px;
  border-bottom:1Px solid #eee;
  background:#fff;
  opacity:1;
  box-sizing: border-box;
  &.opacitySub{
    background: none;
    border-bottom:0px solid #ddd;
    opacity:0;
  }
  &.fixed{
    position: fixed;
    top:46px;
    left:0;
    right:0;
  }
  .sub-content{
    padding:0 20px;
    line-height:40px;
    box-sizing:border-box;
    color:#333;
    a{
      color:#333;
      margin-right:40px;
      font-size: 13px;
    }
  }
  &.iphone-normal{
    top:62px;
  }
  &.iphoneX{
    top:84px;
  }
  &.sole-fixed{
    transition: none;
    position: fixed;
    left:0;
    right:0;
    top:0;
  }
}
@-webkit-keyframes toTop {
  from {
    top: 10px;
  }

  to {
    top: 0;
  }
}
</style>



