<template>
  <div class="about-faq-main">
    <Header
        :fixed="fixed" 
        :fullView="fullView" 
        :right="right"
        @onLeft="goBack"
        :customClass="customClass"
      >
      <mt-navbar class="faq-tab" v-model="selected">
        <mt-tab-item id="2">私募基金</mt-tab-item>
        <mt-tab-item id="1">公募基金</mt-tab-item>
      </mt-navbar>
    </Header>
    <mt-tab-container v-model="selected" :swipeable="true">
      <mt-tab-container-item id="2">
        <div class="faq-main">
          <ul>
            <li>
              <div class="titleQ">
                <i class="iconQuestion"></i>
                <p class="textQuestion">公募基金与私募基金有哪些区别？</p>
              </div>
              <div class="contentA">
                <i class="iconAnswer"></i>
                <div class="textAnswer">
                  <table>
                    <thead>
                      <tr>
                        <th width="40%"></th>
                        <th width="30%">私募基金</th>
                        <th width="30%">公募基金</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <td>募集对象</td>
                        <td>经确认的特定对象</td>
                        <td>公众</td>
                      </tr>
                      <tr>
                        <td>募集方式</td>
                        <td>非公开</td>
                        <td>公开</td>
                      </tr>
                      <tr>
                        <td>信息披露</td>
                        <td>对信息披露要求较低，具有较强的保密性</td>
                        <td>对信息披露有非常严格的要求，其投资目标、投资组合等信息都要披露</td>
                      </tr>
                      <tr>
                        <td>投资范围</td>
                        <td>由基金合同约定</td>
                        <td>在投资品种、投资比例、投资与基金类型的匹配上有严格限制</td>
                      </tr>
                      <tr>
                        <td>业绩报酬</td>
                        <td>除固定管理费外，收取业绩报酬</td>
                        <td>只收取固定管理费</td>
                      </tr>
                      <tr>
                        <td>投资门槛</td>
                        <td>起投金额不少于100万追加资金至少为10万或10万的整数倍</td>
                        <td>起投金额较小，一般1000元（含）以上，定投也有100元起</td>
                      </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </li>
            <li>
              <div class="titleQ">
                <i class="iconQuestion"></i>
                <p class="textQuestion">私募基金有哪些类型？</p>
              </div>
              <div class="contentA">
                <i class="iconAnswer"></i>
                <div class="textAnswer">
                  <p>契约型基金，是指未成立法律实体，而是通过契约形式设立私募基金，基金管理人、投资者和其他基金参与主体按照契约约定行使相应权利，承担相应义务和责任。</p>
                  <p>公司型基金，是指投资者按照《公司法》，通过出资形成一个独立法人实体——基金公司，由基金公司自行或者通过委托专门的基金管理人机构进行管理，投资者既是基金份额持有者又是基金公司股东，按照公司章程行使相应权利、承担相应义务和责任。</p>
                  <p>合伙型基金，是指投资者依据《合伙企业法》，成立投资基金有限合伙企业，由普通合伙人对合伙债务承担无限连带责任，由基金管理人具体负责投资运作。</p>
                </div>
              </div>
            </li>
            <li>
              <div class="titleQ">
                <i class="iconQuestion"></i>
                <p class="textQuestion">私募基金的投资范围都有哪些？</p>
              </div>
              <div class="contentA">
                <i class="iconAnswer"></i>
                <p class="textAnswer">目前而言，私募基金可以投资于中国证券市场上的股票、债券、封闭式基金、开放式基金、央行票据、资产支持证券、金融衍生品、期货、期权以及中国证券监会规定可以投资的其他投资品种。私募基金投资范围和投资限制需要在基金合同中约定，并根据基金合同约定执行。私募基金包含天使基金，公司未成立前就融资的风险投资VC，私募股权PE，上市前投资Pro-IPO及上市后私募投资（PIPE）等。</p>
              </div>
            </li>
            <li>
              <div class="titleQ">
                <i class="iconQuestion"></i>
                <p class="textQuestion">如何知道私募基金的发行是否合法合规?</p>
              </div>
              <div class="contentA">
                <i class="iconAnswer"></i>
                <p class="textAnswer">根据《证券投资基金法》、《私募投资基金监督管理暂行办法》等法律法规规定，各类私募基金管理人应当通过中国证券投资基金业协会的私募基金登记备案系统办理登记备案手续。否则，不得从事私募基金的发行等业务活动。私募基金也必须向基金行业协会备案后方可开始募集。</p>
              </div>
            </li>
            <li>
              <div class="titleQ">
                <i class="iconQuestion"></i>
                <p class="textQuestion">私募基金投资者是否有人数限制？</p>
              </div>
              <div class="contentA">
                <i class="iconAnswer"></i>
                <p class="textAnswer">根据《证券投资基金法》、《公司法》、《合伙企业法》等法律规定，单只私募基金投资人数累计不得超过特定人数。其中有限合伙型基金、有限公司型基金的投资者累计不得超过50人；契约型基金、股份公司型基金的投资者累计不得超过200人。</p>
              </div>
            </li>
          </ul>
        </div>
      </mt-tab-container-item>
      <mt-tab-container-item id="1">
        <div class="faq-main">
          <ul>
            <li>
              <div class="titleQ">
                <i class="iconQuestion"></i>
                <p class="textQuestion">公募基金交易中的T日是指哪一天，T+N代表什么含义？</p>
              </div>
              <div class="contentA">
                <i class="iconAnswer"></i>
                <p class="textAnswer">T日为交易申请日（也可成为委托日），如果申请日非基金交易日，或者下单时间超过当个交易日的15:00，则T日为下一个交易日。T+N是指T日后的第N个交易日。</p>
              </div>
            </li>
            <li>
              <div class="titleQ">
                <i class="iconQuestion"></i>
                <p class="textQuestion">投资公募基金后是否可以提前退出？</p>
              </div>
              <div class="contentA">
                <i class="iconAnswer"></i>
                <p class="textAnswer">一般基金会在T+1日（QDII基金一般为T+2）确认，只要确认成功，开放式基金无投资期限的，即可随时进行赎回操作。</p>
              </div>
            </li>
            <li>
              <div class="titleQ">
                <i class="iconQuestion"></i>
                <p class="textQuestion">公募基金赎回一般需多久到账？资金如何返还？</p>
              </div>
              <div class="contentA">
                <i class="iconAnswer"></i>
                <div class="textAnswer">
                  <p>公募基金赎回后由支付机构直接打款到银行卡，具体详情请留意银行入账信息；</p>
                  <p>基金赎回到账时间如下： <br/>
                  货币基金：T+1~T+2日；<br/>
                  债券型/股票型/混合型基金：T+2~T+4日； <br>
                  QDII：T+4~T+15日(具体以基金合同约定为准)。
                  </p>
                </div>
              </div>
            </li>
            <li>
              <div class="titleQ">
                <i class="iconQuestion"></i>
                <p class="textQuestion">何为基金分红？基金分红有什么作用？</p>
              </div>
              <div class="contentA">
                <i class="iconAnswer"></i>
                <div class="textAnswer">
                  <p>基金分红的本质是将基金收益的一部分，以现金或者基金份额的形式派发给投资人，故一般开放式基金分红有两种方式：现金分红和红利再投资。</p>
                  <p>现金分红：指基金发放的红利以现金形式直接打入投资者购买基金的账户，可以节省这部分赎回费用；</p>
                  <p>红利再投资：指发放的红利按照除权日当天的净值转为基金份额，并计入基金总份额，相当于再次申购，但不用申购费。</p>
                  <p>一般而言，基金默认的分红方式为现金分红，投资者可以对默认分红方式进行修改，需在权益登记日（基金分红公告中会明确告知）前一个交易日下午三点前修改，方对当次分红有效。货币市场基金只有红利再投资一种分红方式，且无法修改。</p>
                  <p>需要注意，基金分红与股票不同，分红并不会增加投资者的收益，只不过是将原本属于投资者的收益以现金或者份额的形式折算给投资者，而分红后基金净值会降低。</p>
                </div>
              </div>
            </li>
            <li>
              <div class="titleQ">
                <i class="iconQuestion"></i>
                <p class="textQuestion">已成功绑定的银行卡是否支持换卡？</p>
              </div>
              <div class="contentA">
                <i class="iconAnswer"></i>
                <div class="textAnswer">
                  <p>为了确保资金的安全性，本公司遵循同卡进出原则。如无卡片遗失、冻结、销户等特殊情况，暂不支持用户自行更换已绑定的银行卡。如遇银行卡丢失或者急需更换，请至我司营业柜台办理。</p>
                </div>
              </div>
            </li>
            <li>
              <div class="titleQ">
                <i class="iconQuestion"></i>
                <p class="textQuestion">公募基金有哪些类别，相应的特点和风险如何？</p>
              </div>
              <div class="contentA">
                <i class="iconAnswer"></i>
                <div class="textAnswer">
                  <table>
                    <thead>
                      <tr>
                        <th width="50%">基金类型</th>
                        <th width="30%">特点</th>
                        <th width="20%">风险</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <td>货币基金</td>
                        <td>无申购赎回费用，赎回到账快</td>
                        <td>极低风险</td>
                      </tr>
                      <tr>
                        <td>债券型基金</td>
                        <td>稳健投资的好选择</td>
                        <td>风险较低</td>
                      </tr>
                      <tr>
                        <td>股票型基金</td>
                        <td>紧跟价值行业和热点主题</td>
                        <td>高风险高收益</td>
                      </tr>
                      <tr>
                        <td>混合型基金</td>
                        <td>偏股、偏债、股债平衡型配置，比例相对灵活</td>
                        <td>适中的风险和回报</td>
                      </tr>
                      <tr>
                        <td>指数基金</td>
                        <td>跟踪指数，与市场同步成长，广泛地分散投资，定投的好选择</td>
                        <td>较低的非系统风险</td>
                      </tr>
                    </tbody>
                  </table>
                  <p class="mt10">风险等级排名：股票型基金>混合型基金>债券型基金>货币基金</p>
                  <p>友情提示：所有投资都存在风险，投资人应根据自身的投资偏好和风险承受能力进行独立判断和作出决策，投资需谨慎。</p>
                </div>
              </div>
            </li>
          </ul>
        </div>
      </mt-tab-container-item>
   </mt-tab-container>
   <div class="footSlot"></div>
   <div class="hotline">
     <div class="service">
       <i class="iconService"></i>在线咨询
     </div>
     <div class="tel" @click="callTel">
       <i class="iconTel"></i>电话咨询
     </div>
   </div>
   <div class="footIphoneX"></div>
  </div>
</template>
<script>
  import Header from 'components/Header'
  import utils from 'utils'
  export default {
    name: 'Faq',
    data() {
      return {
        selected: '2',
        fixed: true,
        fullView: true,
        right: {
          show: false
        },
        back: {
          show: true,
          text: '',
          icon: 'icon-back',
        },
        customClass: 'self-header',
      }
    },
    components: {
      Header
    },
    mounted() {
      let tab = utils.query2params(location.search.substr(1)).tab
      if (tab === 'private') {
        this.selected = '2'
      }
    },
    computed: {
    },
    methods: {
      callTel() {
        // bridge.callTel({
        //   tel: '4006881888'
        // })
      },
      goBack(){
        this.$router.back()
      }
    }
  }
</script>
<style>
.about-faq-main {
  .header-wrapper .header {
    &.self-header {
      font-size: 17px;
      i.left{
        line-height: 45px;
        font-size: 28px;
      }
    }
  }
  .faq-tab {
    .mint-tab-item {
      margin: 0 50px;
      padding: 15px 0 12px;
    }
    .mint-tab-item-label {
      color: #666;
      font-size: 16px;
    }
  }
  .mint-navbar .mint-tab-item.is-selected {
    border-bottom: 3px solid #FF541B;
  }
  .mint-navbar .mint-tab-item.is-selected .mint-tab-item-label {
    color: #333;
    font-weight: 500;
  }
}
</style>

<style scoped>
    .about-faq-main {
      .faq-main {
        li {
          background: #fff;
          margin-top: 10px;
          font-size: 15px;
          .titleQ {
            padding: 10px 20px 10px 15px;
            border-bottom: 1Px solid #F2F2F2;
            color: #333;
            display: flex;
            .iconQuestion {
              margin: 1px 10px 0 0;
              width: 20px;
              height: 20px;
              background: url(./images/iconQuestion.png) no-repeat center center;
              background-size: contain;
            }
            .textQuestion {
              flex: 1;
            }
          }
          .contentA {
            padding: 5px 20px 10px 15px;
            color: #666;
            line-height: 1.8;
            display: flex;
            .iconAnswer {
              margin: 3px 10px 0 0;
              width: 20px;
              height: 20px;
              background: url(./images/iconAnswer.png) no-repeat center center;
              background-size: contain;
            }
            .textAnswer {
              flex: 1;
              table {
                margin-top: 4px;
                border-collapse: collapse;
                line-height: 1.5;
                th {
                  background: #f6f6f6;
                  border: 1Px solid #ddd;
                  padding: 4px 1px;
                  /* font-size: 9px; */
                  font-weight: bold;
                  &:nth-child(1) {
                    width: 20%;
                  }
                }
                td {
                  border: 1Px solid #ddd;
                  padding: 5px;
                  /* font-size: 8px; */
                  text-align: left;
                }
              }
              p {
                margin-bottom: 10px;
                &:last-child {
                  margin-bottom: 0;
                }
              }
            }
          }
        }
        .mt10 {
          margin-top: 10px;
        }
      }
      .footSlot {
        height: 60px
      }
      .hotline {
        z-index: 999;
        position: fixed;
        bottom: 0;
        width: 100%;
        height: 50px;
        line-height: 50px;
        display: flex;
        text-align: center;
        font-size: 17px;
        .service {
          flex: 1;
          color: #E7E7E7;
          background: #B0B0B0;
          .iconService {
            display: inline-block;
            margin: 0 10px -8px 0;
            width: 26px;
            height: 26px;
            background: url(./images/iconUnService.png) no-repeat center center;
            background-size: contain;
          }
        }
        .tel {
          flex: 1;
          color: #FFF;
          background: #FD5729;
          .iconTel {
            display: inline-block;
            margin: 0 5px -5px 0;
            width: 20px;
            height: 20px;
            background: url(./images/iconTel.png) no-repeat center center;
            background-size: contain;
          }
        }
      }
      .footIphoneX {
        display: none;
      }
    }
    @media only screen and (device-width: 375px) and (device-height: 812px) and (-webkit-device-pixel-ratio: 3) {
      .about-faq-main {
        .footSlot {
          height: 94px;
        }
        .hotline {
          bottom: 34px;
        }
        .footIphoneX {
          display: block;
          width: 100%;
          background: #f6f6f6;
          height: 34px;
          position: fixed;
          bottom: 0;
          left: 0;
        }
      }
    }
</style>


